@import "./variables";

.breadcrumbs {
    padding: 30px 0px 10px;
    margin-bottom: 30px;
    border-bottom: 1px solid $border-grey;
    &__list {
        display: flex;
        align-items: center;
        list-style-type: none; }
    &__item {
        & + & {
            margin-left: 20px;
            position: relative;
            &::before {
                font-size: 12px;
                content: '>';
                position: absolute;
                display: inline-block;
                top: 55%;
                left: -7px;
                transform: translate(-100%, -50%); } } }
    &__link {
        font-size: 12px;
        @include transition(color);
        &[href]:hover {
            color: $light-blue-hover; } } }
