@import "./variables";

.dropdown {
    position: relative;
    display: inline-block;
    &__trigger {
        position: relative;
        z-index: 3;
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            top: 7px;
            right: -16px;
            margin-left: 4px;
            transform: translate(-3px, 0);
            @include transition(all);
            border: 4px solid transparent {
                top: 4px solid $text-black; } }
        &.active::after {
            transform: translate(-3px, -3px) rotate(180deg); }
        &-text {
            &, & * {
                font-size: 16px;
                font-weight: 400;
                text-transform: none; } } }
    &__list {
        overflow: hidden;
        position: absolute;
        min-width: 180px;
        width: calc( 100% + 10px );
        top: 105%;
        left: 0;
        background-color: #fff;
        list-style-type: none;
        @include transition(all);
        opacity: 0;
        transform: translate(-5px, -20px);
        visibility: hidden;
        &.active {
            opacity: 1;
            transform: translate(-5px, 0px);
            visibility: visible; } }
    &__item {
        cursor: pointer; }
    select {
        visibility: hidden;
        opacity: 0;
        height: 1px;
        width: 1px;
        position: absolute; }
    .js-dropdown {
        &-select {
            display: none; } } }

.filter {
    .dropdown {
        &__trigger {
            &-text {
                &, & * {
                    color: $light-blue-hover; } }
            &::after {
                border: 4px solid transparent {
                    top: 4px solid $light-blue-hover; } } }
        &__list {
            box-shadow: 0 4px 14px 3px rgba(0, 0, 0, .1); }
        &__item {
            a {
                width: 100%;
                display: inline-block;
                padding: 5px;
                @include transition(background-color); }
            &:hover {
                a {
                    background-color: $border-grey; } } } } }
