.item {
  padding-bottom: 80px;
}

.item__body {
  background-color: #fff;
  padding: 30px;
  border: 1px solid #dadbdc;
  position: relative;
  z-index: 2;
}

.item__body_grid {
  display: grid;
  grid-template-columns: 1fr 330px;
  column-gap: 30px;
  row-gap: 20px;
}

.item__name {
  font-weight: 700;
  font-size: 26px;
  grid-column: 1 / 3;
}

.item__top-wrap {
  display: flex;
  align-items: stretch;
}

.item__data {
  flex-shrink: 0;
  max-width: 330px;
}

.item__slider {
  flex-grow: 1;
}

.item__slider-wrap {
  width: 100%;
  min-width: 0;
}

.item__tabs .item__body {
  margin-top: -1px;
}

.item__tabs {
  margin-top: 30px;
}

.item__tabs-top {
  width: 100%;
  display: flex;
  align-items: stretch;
}

.item-thumbs {
  min-width: 0;
  max-height: 70px;
  height: 100%;
  margin-top: 20px;
  overflow: hidden;
}

.item-thumbs__item {
  border: 1px solid #dadbdc;
}

.item-thumbs__item.swiper-slide-thumb-active {
  opacity: .7;
  border: 3px solid #2ca8e1;
}

.item-thumbs__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.item-slider {
  min-width: 0;
  position: relative;
  overflow: hidden;
  width: 100%;
}

.item-slider__compare {
  position: absolute;
  top: 20px;
  left: 20px;
  height: 40px;
  width: 40px;
  border-radius: 100%;
  background-color: #fff;
  z-index: 3;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
  transition: background-color 0.25s ease-out 0s;
}

.item-slider__compare:hover {
  background-color: #dadbdc;
}

.item-slider__compare:hover svg {
  fill: #52bbec;
}

.item-slider__compare svg {
  transition: fill 0.25s ease-out 0s;
  position: absolute;
  height: 15px;
  width: 15px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #a8a9aa;
}

.item-slider__control {
  position: absolute;
  height: 40px;
  width: 40px;
  background-color: rgba(0, 0, 0, 0.4);
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  transition: background-color 0.25s ease-out 0s;
}

.item-slider__control:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.item-slider__control svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #ffffff;
  height: 40%;
}

.item-slider__control_prev {
  left: 20px;
}

.item-slider__control_next {
  right: 20px;
}

.item-slider__control_next svg {
  transform: translate(-50%, -50%) rotate(180deg);
}

.item-slider__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.item-slider__slide {
  position: relative;
  padding-bottom: 55%;
  border: 1px solid #dadbdc;
}

.data-block__title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
  display: inline-block;
  text-transform: uppercase;
}

.data-block__list {
  list-style-type: none;
}

.data-block__item_bold {
  font-weight: 700;
}

.data-block__price {
  display: inline-block;
  font-size: 26px;
  padding: 10px 20px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #dadbdc;
  font-weight: 700;
  margin-bottom: 25px;
}

.data-block__small-text {
  font-size: 14px;
  line-height: 150%;
  color: #a8a9aa;
  display: inline-block;
  margin-bottom: 20px;
}

.data-block__link {
  color: #2ca8e1;
  font-size: 16px;
  display: inline-block;
  margin-top: 5px;
  cursor: pointer;
  transition: color 0.25s ease-out 0s;
}

.data-block__link:hover {
  color: #52bbec;
}

.data-block__order {
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 4px;
}

.data-block__order, .data-block__price {
  width: 100%;
  max-width: 270px;
}

.data-block_flex {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.data-block + .data-block {
  margin-top: 35px;
}

@media screen and (max-width: 1020px) {
  .item__body {
    padding: 15px;
  }
  .item__body_grid {
    grid-template-columns: 1fr 280px;
  }
}

@media screen and (max-width: 870px) {
  .item__name {
    padding: 0px 15px;
    grid-column: 1 / 2;
    order: 1;
  }
  .item__slider-wrap {
    order: 0;
    width: 100%;
  }
  .item__data {
    padding: 0px 15px;
    order: 2;
  }
  .item__body_grid {
    padding: 0 0 15px;
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 768px) {
  .item__tabs .item__body {
    margin-top: 10px;
  }
  .item__tabs-top {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 630px) {
  .item__name {
    font-size: 22px;
  }
}

@media screen and (max-width: 540px) {
  .item__name {
    font-size: 18px;
  }
  .item__thumbs {
    margin: 10px 15px 0px;
    max-height: 50px;
  }
  .item-slider__compare {
    height: 30px;
    width: 30px;
    right: 10px;
    top: 10px;
    left: auto;
  }
  .item-slider__control {
    height: 20px;
    width: 20px;
  }
  .item-slider__control_prev {
    left: 1px;
  }
  .item-slider__control_next {
    right: 1px;
  }
  .data-block__title {
    font-size: 16px;
  }
  .data-block__item {
    font-size: 14px;
  }
  .data-block__price {
    font-size: 16px;
  }
}
