.footer {
  padding: 55px 0px 0px;
  background-color: #0357a3;
}

.footer .header__social {
  margin-top: 30px;
  height: 25px;
  width: 25px;
}

.footer__wrap {
  display: grid;
  grid-template-columns: 1fr 340px 300px;
  column-gap: 25px;
  margin-bottom: 55px;
}

.footer__title {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 600;
  color: #fff;
  padding-bottom: 10px;
  border-bottom: 1px solid #4f8abf;
  margin-bottom: 15px;
}

.footer__list {
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: repeat(3, 1fr);
  column-gap: 30px;
}

.footer__list_two-rows {
  grid-template-rows: repeat(2, 1fr);
}

.footer__list + .footer__list {
  margin-left: 35px;
}

.footer__item {
  list-style-type: none;
  display: inline-block;
}

.footer__link {
  padding: 8px 0px;
  display: inline-block;
}

.footer__link:hover {
  text-decoration: underline;
}

.footer__row {
  display: flex;
  align-items: flex-start;
}

.footer__text {
  color: #bcd2e6;
  font-size: 14px;
}

.footer__socials {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.footer__social {
  display: inline-block;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: #fff;
  position: relative;
}

.footer__social + .footer__social {
  margin-left: 20px;
}

.footer__social:hover {
  transform: translateY(-5px);
}

.footer__social svg {
  max-width: 25px;
  max-height: 20px;
  width: 100%;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer__subscribe {
  position: relative;
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.footer__subscribe-input {
  background-color: #fff;
  display: block;
  width: 100%;
  padding: 11.5px 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  color: #1c1d1e;
  border: none;
}

.footer__subscribe-input:focus {
  outline: none;
}

.footer__enter {
  display: inline-block;
  width: 60px;
  background-color: #2ca8e1;
  height: 40px;
  cursor: pointer;
  border-radius: 0px;
  border: none;
  flex-shrink: 0;
  position: relative;
}

.footer__enter svg {
  width: 25px;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer__enter:focus {
  outline: none;
}

.footer__bottom {
  background-color: #003d75;
  padding: 20px 0px;
}

.footer__bottom-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__copyright {
  font-size: 14px;
  color: #fff;
  opacity: .5;
}

.footer__copyright + .footer__copyright {
  margin-left: 10px;
}

.footer a.footer__copyright:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1020px) {
  .footer__wrap {
    row-gap: 40px;
  }
}

@media screen and (max-width: 740px) {
  .footer__wrap {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 540px) {
  .footer__bottom-wrap {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__copyright + .footer__copyright {
    margin-left: 0px;
    margin-top: 15px;
  }
}

@media screen and (max-width: 380px) {
  .footer__list {
    width: 100%;
    column-gap: 15px;
    justify-items: space-between;
  }
}
