.color {
  display: inline-block;
  position: relative;
  border-radius: 100%;
  height: 38px;
  width: 38px;
  border: 2px solid #dadbdc;
  flex-shrink: 0;
}

.color + .color {
  margin-left: 5px;
}

.color span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 30px;
  width: 30px;
  display: inline-block;
  border-radius: 100%;
  transition: trasnform 0.25s ease-out 0s;
}

.color.active span {
  transform: translate(-50%, -50%) scale(1.15);
}
