@import "./variables";

.index-catalog {
    margin-bottom: $vertical-padding;
    &__top {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 30px;
        .button + .button {
            margin-left: 60px; } }
    &__body {
        display: grid;
        grid-template-columns: 1fr; }
    &__item {
        height: 200px;
        width: 100%;
        align-items: stretch;
        & + & {
            margin-top: 30px; }
        &:nth-child(even) {
            flex-direction: row-reverse; } } }

@media screen and ( max-width: 960px ) {
    .index-catalog {
        &__item {
            flex-direction: column-reverse;
            &:nth-child(even) {
                flex-direction: column-reverse; } } } }

@media screen and ( max-width: 740px ) {
    .index-catalog {
        &__top {
            flex-direction: column;
            align-items: center;
            .button + .button {
                margin-left: 0px;
                margin-top: 25px; } } } }
