.search {
  width: 100%;
  height: 100%;
  align-self: center;
  max-width: 220px;
  border-radius: 4px;
  border: 1px solid #dadbdc;
  position: relative;
  display: flex;
  align-items: stretch;
  margin: 0px 15px;
}

.search__input {
  background-color: #f8f9fa;
  font-size: 16px;
  padding: 12px 0px 10px 20px;
  border: none;
  width: 100%;
  height: 100%;
  display: block;
}

.search__button {
  background-color: #f8f9fa;
  flex-shrink: 0;
  padding: 10px 20px 10px 5px;
}

.search__button img, .search__button svg {
  fill: #dadbdc;
  margin-right: 0;
}

.search_mobile {
  display: none;
  border-radius: 0px;
  border: none;
  margin: 15px 0px !important;
}

.search_mobile .search__input {
  font-size: 12px;
  padding: 10px;
}

.search_mobile .search__enter {
  padding: 0px 5px;
  font-size: 12px;
}
