@import "./variables";

.catalog {
    padding-bottom: $vertical-padding;
    &__body {
        display: flex;
        align-items: flex-start; }
    &__grid {
        margin-left: 30px;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
        column-gap: 30px;
        row-gap: 30px; }
    &__item {
        @include transition(box-shadow);
        &:hover {
            box-shadow: 0 0 12px 2px rgba(0, 0, 0, .1); } }
    &__aside {
        flex-shrink: 0;
        position: sticky;
        top: 100px;
        width: 270px; } }

.catalog-aside {
    &__mobile-trigger {
        display: none;
        background-color: #fff;
        padding: 15px;
        width: 100%;
        border: 1px solid $border-grey; }
    &__list {
        list-style-type: none; }
    &__item {
        & + & {
            margin-top: 2px; } }
    &__link {
        display: flex;
        align-items: center;
        border: 1px solid $border-grey;
        width: 100%;
        background-color: #fff;
        font-size: 16px;
        text-transform: uppercase;
        font-weight: 700;
        padding: 5px 20px;
        position: relative;
        height: 50px;
        span {
            position: relative;
            z-index: 3;
            @include transition(color); }
        &:not(.catalog-aside__link_sale) {
            &::before {
                @include transition(width);
                content: '';
                display: inline-block;
                position: absolute;
                height: calc( 100% + 2px );
                top: -1px;
                left: 0;
                background-color: $light-blue;
                width: 4px; }
            &:hover, &.active {
                span {
                    color: #ffffff; }
                &::before {
                    width: 100%; } } }
        &_sale {
            background-color: #fab206;
            @include transition(background-color);
            svg {
                fill: #ffffff;
                margin-right: 10px; }
            span {
                color: #ffffff; }
            &:hover {
                background-color: #fac038; } } } }

@media screen and ( max-width: 920px ) {
    .catalog {
        &__body {
            flex-direction: column;
            align-items: stretch; }
        &__grid {
            margin-left: 0px;
            margin-top: 30px; } }

    .catalog-aside {
        width: 100%;
        &__mobile-trigger {
            display: inline-block; }
        &__list {
            display: none; } } }
