@import "./variables";

.reviews {
    padding-bottom: $vertical-padding; }

.review-main {
    background-color: #fff;
    padding: 30px;
    border: 1px solid $border-grey;
    margin-bottom: $vertical-padding;
    &__top {
        display: flex;
        align-items: center;
        margin-bottom: 30px; }
    &__img {
        border: 5px solid #e5eef6;
        border-radius: 100%;
        height: 140px;
        width: 140px;
        object-fit: cover;
        flex-shrink: 0; }
    &__img + &__credits {
        margin-left: 20px; }
    &__name {
        display: block;
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 5px; }
    &__content {
        & > * + * {
            display: inline-block;
            margin-top: 1em; }
        * {
            line-height: 150%; }
        h1, h2, h3, h4, h5 {
            line-height: 120%; }
        ul {
            list-style-position: inside; } } }

.reviews-item {
    list-style-type: none;
    display: block;
    width: 100%;
    display: flex;
    background-color: #fff;
    padding: 30px;
    border: 1px solid $border-grey;
    & + & {
        margin-top: 10px; }
    &__media {
        display: flex;
        flex-direction: column;
        align-items: center; }
    &__img {
        height: 100px;
        width: 100px;
        flex-shrink: 0;
        object-fit: cover;
        border-radius: 100%;
        border: 5px solid #e5eef6; }
    &__img + &__logo {
        margin-top: 10px; }
    &__logo {
        max-width: 100px;
        width: 100%;
        height: auto;
        object-fit: contain;
        flex-shrink: 0; }
    &__media + &__content {
        margin-left: 20px; }
    &__title {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 5px; }
    &__text {
        line-height: 150%;
        font-size: 16px; } }

@media screen and ( max-width: 740px ) {
    .review-main {
        &__top {
            flex-direction: column; }
        &__credits {
            align-items: center;
            display: flex;
            flex-direction: column; }
        &__name {
            display: inline-block;
            text-align: center; }
        &__position {
            text-align: center;
            display: inline-block; }
        &__img + &__credits {
            margin-left: 0px;
            margin-top: 20px; } }

    .reviews-item {
        flex-direction: column;
        align-items: flex-start;
        padding: 20px;
        &__media {
            flex-direction: row; }
        &__img + &__logo {
            margin-top: 0px;
            margin-left: 20px; }
        &__media + &__content {
            margin-left: 0px;
            margin-top: 20px; } } }
