@import "./variables";

.section-title {
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    color: $text-black;
    padding: 10px 20px 20px;
    width: 100%;
    margin-bottom: 30px;
    position: relative;
    border: {
        left: 25px solid $border-grey;
        bottom: 1px solid $border-grey; }
    &_blue {
        border: {
            left: 25px solid #4f8abf;
            bottom: 1px solid #4f8abf; }
        color: #ffffff; } }

@media screen and ( max-width: 740px ) {
    .section-title {
        text-align: center;
        border-left: none;
        padding-top: 0;
        padding-bottom: 10px;
        font-size: 28px;
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 100%);
            height: 4px;
            width: 120px;
            background-color: $border-grey; }
        &_blue {
            &::after {
                background-color:  #4f8abf; } } } }

@media screen and ( max-width: 430px ) {
    .section-title {
        font-size: 20px;
        line-height: 24px; } }
