.catalog {
  padding-bottom: 80px;
}

.catalog__body {
  display: flex;
  align-items: flex-start;
}

.catalog__grid {
  margin-left: 30px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  column-gap: 30px;
  row-gap: 30px;
}

.catalog__item {
  transition: box-shadow 0.25s ease-out 0s;
}

.catalog__item:hover {
  box-shadow: 0 0 12px 2px rgba(0, 0, 0, 0.1);
}

.catalog__aside {
  flex-shrink: 0;
  position: sticky;
  top: 100px;
  width: 270px;
}

.catalog-aside__mobile-trigger {
  display: none;
  background-color: #fff;
  padding: 15px;
  width: 100%;
  border: 1px solid #dadbdc;
}

.catalog-aside__list {
  list-style-type: none;
}

.catalog-aside__item + .catalog-aside__item {
  margin-top: 2px;
}

.catalog-aside__link {
  display: flex;
  align-items: center;
  border: 1px solid #dadbdc;
  width: 100%;
  background-color: #fff;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 700;
  padding: 5px 20px;
  position: relative;
  height: 50px;
}

.catalog-aside__link span {
  position: relative;
  z-index: 3;
  transition: color 0.25s ease-out 0s;
}

.catalog-aside__link:not(.catalog-aside__link_sale)::before {
  transition: width 0.25s ease-out 0s;
  content: '';
  display: inline-block;
  position: absolute;
  height: calc( 100% + 2px);
  top: -1px;
  left: 0;
  background-color: #2ca8e1;
  width: 4px;
}

.catalog-aside__link:not(.catalog-aside__link_sale):hover span, .catalog-aside__link:not(.catalog-aside__link_sale).active span {
  color: #ffffff;
}

.catalog-aside__link:not(.catalog-aside__link_sale):hover::before, .catalog-aside__link:not(.catalog-aside__link_sale).active::before {
  width: 100%;
}

.catalog-aside__link_sale {
  background-color: #fab206;
  transition: background-color 0.25s ease-out 0s;
}

.catalog-aside__link_sale svg {
  fill: #ffffff;
  margin-right: 10px;
}

.catalog-aside__link_sale span {
  color: #ffffff;
}

.catalog-aside__link_sale:hover {
  background-color: #fac038;
}

@media screen and (max-width: 920px) {
  .catalog__body {
    flex-direction: column;
    align-items: stretch;
  }
  .catalog__grid {
    margin-left: 0px;
    margin-top: 30px;
  }
  .catalog-aside {
    width: 100%;
  }
  .catalog-aside__mobile-trigger {
    display: inline-block;
  }
  .catalog-aside__list {
    display: none;
  }
}
