.gallery {
  padding-bottom: 80px;
}

.gallery__grid {
  display: grid;
  column-gap: 30px;
  row-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  margin-bottom: 40px;
}

.gallery__grid_video {
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
}

.gallery__grid_video .gallery-item {
  border: none;
}

.gallery__grid_video .gallery-item__body {
  padding: 15px 0px 0px;
  background-color: transparent;
}

.gallery__grid_video .gallery-item__name {
  font-size: 16px;
  font-weight: 400;
  text-align: left;
}

.gallery-item {
  border: 1px solid #dadbdc;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
}

.gallery-item__media {
  display: block;
  position: relative;
  padding-bottom: 75%;
  overflow: hidden;
}

.gallery-item__media_video::before {
  background-image: url(../img/play-btn.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  height: 60px;
  width: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  pointer-events: none;
  transition: transform 0.25s ease-out 0s;
}

.gallery-item__media_video:hover::before {
  transform: translate(-50%, -50%) scale(1.07);
}

.gallery-item__media:hover img {
  transform: scale(1.01);
}

.gallery-item__media img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  transition: transform 0.25s ease-out 0s;
  transition-duration: .5s;
}

.gallery-item__body {
  background-color: #fff;
  padding: 25px 10px;
  height: 100%;
}

.gallery-item__name {
  font-size: 18px;
  font-weight: 700;
  display: inline-block;
  width: 100%;
  text-align: center;
  line-height: 130%;
}

@media screen and (max-width: 870px) {
  .gallery__grid_video {
    justify-content: center;
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(300px, 480px));
  }
}

@media screen and (max-width: 470px) {
  .gallery__grid_video {
    grid-template-columns: repeat(auto-fill, minmax(250px, 480px));
  }
}
