.popup {
  position: fixed;
  z-index: 22;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.25s ease-out 0s;
  opacity: 0;
  overflow-y: auto;
  visibility: hidden;
  pointer-events: none;
  padding: 10px;
}

.popup__media {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.popup__body {
  transition: all 0.25s ease-out 0s;
  transform: translateY(-50px);
  opacity: 0;
  width: 100%;
  position: relative;
  height: auto;
}

.popup__body_media {
  max-width: 90%;
  height: 90%;
}

.popup__body_media .popup__close {
  background-color: rgba(0, 0, 0, 0.6);
}

.popup__body_small {
  max-width: 650px;
}

.popup__body_big {
  max-width: 1150px;
}

.popup__subtext {
  margin-top: 50px;
  font-size: 18px;
  color: #ffffff;
  text-align: center;
  display: inline-block;
  width: 100%;
}

.popup__close {
  position: absolute;
  top: 0;
  right: 0;
  height: 40px;
  width: 40px;
  z-index: 3;
}

.popup__close::before, .popup__close::after {
  content: '';
  display: inline-block;
  height: 2px;
  width: 22px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.popup__close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.popup__close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.popup.active .popup__body {
  transform: translateY(0px);
  opacity: 1;
}

.popup.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

@media screen and (max-height: 820px) and (max-width: 1100px) {
  .popup__body {
    height: 100%;
  }
}
