@import "./variables";

.table {
    width: 100%;
    border-collapse: collapse;
    &, & tbody {
        display: grid;
        grid-template-columns: repeat(1, 1fr); }
    &__cell {
        display: flex;
        &_bold {
            font-weight: 700; }
        &_column {
            align-items: flex-start;
            flex-direction: column; }
        &_row {
            align-items: center; } }
    &__item {
        position: relative;
        &-img {
            width: 90px;
            height: 50px;
            flex-shrink: 0;
            object-fit: cover;
            border: 1px solid $border-grey;
            margin-bottom: 10px; }
        &-name {
            font-size: 16px;
            font-weight: 400;
            display: inline-block;
            margin-bottom: 10px;
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0; } }
        &-cart {
            z-index: 2;
            padding-left: 0; } }
    &__remove {
        text-align: left;
        font-weight: 400;
        padding: 0;
        text-transform: none;
        color: $text-red;
        border: none;
        &_cross {
            display: none;
            position: absolute;
            top: -5px;
            right: -5px;
            height: 20px;
            width: 20px;
            z-index: 2;
            &::before, &::after {
                content: '';
                display: inline-block;
                width: 12px;
                height: 1px;
                background-color: $text-red;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%); }
            &::before {
                transform: translate(-50%, -50%) rotate(45deg); }
            &::after {
                transform: translate(-50%, -50%) rotate(-45deg); } } }
    &__move {
        display: none;
        position: absolute;
        bottom: -10px;
        transform: translate(0, 100%);
        background-color: $grey;
        height: 20px;
        width: 20px;
        svg {
            position: absolute;
            top: 50%;
            height: 60%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #ffffff; }
        &_left {
            left: 0; }
        &_right {
            right: 0;
            svg {
                transform: translate(-50%, -50%) rotate(180deg); } } }
    tr {
        display: grid;
        grid-template-columns: 390px repeat(2, 1fr);
        overflow: hidden;
        background-color: #fff;
        border: 1px solid $border-grey;
        border-bottom: none;
        &:last-child {
            border-bottom: 1px solid $border-grey; }
        td, th {
            &:nth-child(1) {
                font-weight: 700; } } }
    td, th {
        display: block;
        font-size: 16px;
        padding: 25px 30px; }
    tr + tr {
        border-top: 1px solid $border-grey; } }

@media screen and ( max-width: 960px ) {
    .table {
        tr {
            grid-template-columns: 300px repeat(2, 1fr); }
        td, th {
            padding: 15px 20px; } } }

@media screen and ( max-width: 768px ) {
    .table {
        &__move {
            display: inline-block; }
        &__remove {
            &_cross {
                display: inline-block; } }
        tr {
            grid-template-columns: repeat(2, 1fr);
            &:first-child {
                background-color: transparent;
                margin-bottom: 50px;
                border: none;
                column-gap: 20px;
                overflow: visible;
                align-items: start;
                td, th {
                    position: relative;
                    background-color: #fff;
                    border: 1px solid $border-grey;
                    padding: 10px;
                    &:first-child {
                        display: none; } } }
            &:last-child {
                display: none; } }
        td, th {
            hyphens: manual;
            padding: 15px;
            &:nth-child(1) {
                grid-column: 1 / 3; } } } }
