.table {
  width: 100%;
  border-collapse: collapse;
}

.table, .table tbody {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.table__cell {
  display: flex;
}

.table__cell_bold {
  font-weight: 700;
}

.table__cell_column {
  align-items: flex-start;
  flex-direction: column;
}

.table__cell_row {
  align-items: center;
}

.table__item {
  position: relative;
}

.table__item-img {
  width: 90px;
  height: 50px;
  flex-shrink: 0;
  object-fit: cover;
  border: 1px solid #dadbdc;
  margin-bottom: 10px;
}

.table__item-name {
  font-size: 16px;
  font-weight: 400;
  display: inline-block;
  margin-bottom: 10px;
}

.table__item-name::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.table__item-cart {
  z-index: 2;
  padding-left: 0;
}

.table__remove {
  text-align: left;
  font-weight: 400;
  padding: 0;
  text-transform: none;
  color: #d44242;
  border: none;
}

.table__remove_cross {
  display: none;
  position: absolute;
  top: -5px;
  right: -5px;
  height: 20px;
  width: 20px;
  z-index: 2;
}

.table__remove_cross::before, .table__remove_cross::after {
  content: '';
  display: inline-block;
  width: 12px;
  height: 1px;
  background-color: #d44242;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.table__remove_cross::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.table__remove_cross::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.table__move {
  display: none;
  position: absolute;
  bottom: -10px;
  transform: translate(0, 100%);
  background-color: #a8a9aa;
  height: 20px;
  width: 20px;
}

.table__move svg {
  position: absolute;
  top: 50%;
  height: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #ffffff;
}

.table__move_left {
  left: 0;
}

.table__move_right {
  right: 0;
}

.table__move_right svg {
  transform: translate(-50%, -50%) rotate(180deg);
}

.table tr {
  display: grid;
  grid-template-columns: 390px repeat(2, 1fr);
  overflow: hidden;
  background-color: #fff;
  border: 1px solid #dadbdc;
  border-bottom: none;
}

.table tr:last-child {
  border-bottom: 1px solid #dadbdc;
}

.table tr td:nth-child(1), .table tr th:nth-child(1) {
  font-weight: 700;
}

.table td, .table th {
  display: block;
  font-size: 16px;
  padding: 25px 30px;
}

.table tr + tr {
  border-top: 1px solid #dadbdc;
}

@media screen and (max-width: 960px) {
  .table tr {
    grid-template-columns: 300px repeat(2, 1fr);
  }
  .table td, .table th {
    padding: 15px 20px;
  }
}

@media screen and (max-width: 768px) {
  .table__move {
    display: inline-block;
  }
  .table__remove_cross {
    display: inline-block;
  }
  .table tr {
    grid-template-columns: repeat(2, 1fr);
  }
  .table tr:first-child {
    background-color: transparent;
    margin-bottom: 50px;
    border: none;
    column-gap: 20px;
    overflow: visible;
    align-items: start;
  }
  .table tr:first-child td, .table tr:first-child th {
    position: relative;
    background-color: #fff;
    border: 1px solid #dadbdc;
    padding: 10px;
  }
  .table tr:first-child td:first-child, .table tr:first-child th:first-child {
    display: none;
  }
  .table tr:last-child {
    display: none;
  }
  .table td, .table th {
    hyphens: manual;
    padding: 15px;
  }
  .table td:nth-child(1), .table th:nth-child(1) {
    grid-column: 1 / 3;
  }
}
