@import "./variables";

.hits {
    padding: 60px 0px $vertical-padding;
    &__slider {
        overflow: hidden;
        &-wrap {
            margin-bottom: 40px;
            position: relative; } }
    &__control {
        position: absolute;
        height: 50px;
        width: 50px;
        background-color: $light-blue;
        top: 50%;
        z-index: 3;
        &_prev {
            left: -20px;
            transform: translate(-100%, -50%); }
        &_next {
            right: -20px;
            transform: translate(100%, -50%) rotate(180deg); }
        svg {
            height: 20px;
            width: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #ffffff; }
        &:hover {
            background-color: $light-blue-hover; } } }

@media screen and ( max-width: 740px ) {
    .hits {
        &__control {
            top: auto;
            bottom: 0;
            height: 25px;
            width: 25px;
            svg {
                height: 12px; }
            &_prev {
                left: 0px;
                transform: translate(0%, 0%); }
            &_next {
                right: 0px;
                transform: translate(0%, 0%) rotate(180deg); } }
        &__slider {
            &-wrap {
                padding-bottom: 40px; } } } }
