.reviews {
  padding-bottom: 80px;
}

.review-main {
  background-color: #fff;
  padding: 30px;
  border: 1px solid #dadbdc;
  margin-bottom: 80px;
}

.review-main__top {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.review-main__img {
  border: 5px solid #e5eef6;
  border-radius: 100%;
  height: 140px;
  width: 140px;
  object-fit: cover;
  flex-shrink: 0;
}

.review-main__img + .review-main__credits {
  margin-left: 20px;
}

.review-main__name {
  display: block;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 5px;
}

.review-main__content > * + * {
  display: inline-block;
  margin-top: 1em;
}

.review-main__content * {
  line-height: 150%;
}

.review-main__content h1, .review-main__content h2, .review-main__content h3, .review-main__content h4, .review-main__content h5 {
  line-height: 120%;
}

.review-main__content ul {
  list-style-position: inside;
}

.reviews-item {
  list-style-type: none;
  display: block;
  width: 100%;
  display: flex;
  background-color: #fff;
  padding: 30px;
  border: 1px solid #dadbdc;
}

.reviews-item + .reviews-item {
  margin-top: 10px;
}

.reviews-item__media {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews-item__img {
  height: 100px;
  width: 100px;
  flex-shrink: 0;
  object-fit: cover;
  border-radius: 100%;
  border: 5px solid #e5eef6;
}

.reviews-item__img + .reviews-item__logo {
  margin-top: 10px;
}

.reviews-item__logo {
  max-width: 100px;
  width: 100%;
  height: auto;
  object-fit: contain;
  flex-shrink: 0;
}

.reviews-item__media + .reviews-item__content {
  margin-left: 20px;
}

.reviews-item__title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 5px;
}

.reviews-item__text {
  line-height: 150%;
  font-size: 16px;
}

@media screen and (max-width: 740px) {
  .review-main__top {
    flex-direction: column;
  }
  .review-main__credits {
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  .review-main__name {
    display: inline-block;
    text-align: center;
  }
  .review-main__position {
    text-align: center;
    display: inline-block;
  }
  .review-main__img + .review-main__credits {
    margin-left: 0px;
    margin-top: 20px;
  }
  .reviews-item {
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
  }
  .reviews-item__media {
    flex-direction: row;
  }
  .reviews-item__img + .reviews-item__logo {
    margin-top: 0px;
    margin-left: 20px;
  }
  .reviews-item__media + .reviews-item__content {
    margin-left: 0px;
    margin-top: 20px;
  }
}
