.header {
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  border-bottom: 1px solid #dadbdc;
}

.header_fixed {
  box-shadow: 0 0 14px 3px rgba(0, 0, 0, 0.1);
}

.header_fixed .header__top {
  padding: 10px 60px;
}

.header_fixed .header__logos {
  padding: 10px 0px;
}

.header_fixed .header__compare {
  padding: 22px 20px;
}

.header__top, .header__logos, .header__compare {
  transition: padding 0.25s ease-out 0s;
}

.header__top, .header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__top {
  background-color: #0357a3;
  padding: 20px 60px;
}

.header__top-social {
  display: flex;
  align-items: center;
  list-style-type: none;
}

.header__top-content {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  max-width: 1300px;
}

.header__recall {
  color: #2ca8e1;
  margin-left: 20px;
}

.header__tel {
  display: inline-block;
  vertical-align: middle;
  margin: 0px 10px;
}

.header__tel img, .header__tel svg {
  margin-right: 10px;
  height: 15px;
  width: auto;
}

.header__contact {
  display: flex;
  align-items: center;
}

.header__social {
  position: relative;
  height: 20px;
  width: 20px;
  flex-shrink: 0;
  display: inline-block;
  transition: transform 0.25s ease-out 0s;
}

.header__social:hover {
  transform: translateY(-3px);
}

.header__social_round {
  border-radius: 50%;
  border: 1px solid #ffffff;
}

.header__social-item {
  display: inline-block;
}

.header__social-item + .header__social-item {
  margin-left: 10px;
}

.header__social svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__main {
  padding: 0px 60px;
  align-items: stretch;
  background-color: #fff;
}

.header__compare {
  padding: 32px 20px;
  height: 100%;
  display: inline-block;
  position: relative;
  transition: background-color 0.25s ease-out 0s;
  flex-shrink: 0;
  border-left: 1px solid #dadbdc;
  border-right: 1px solid #dadbdc;
}

.header__compare img, .header__compare svg {
  transition: fill 0.25s ease-out 0s;
  margin-right: 10px;
  vertical-align: baseline;
  fill: #a8a9aa;
}

.header__compare:hover {
  background-color: #dadbdc;
}

.header__compare:hover svg {
  fill: #52bbec;
}

.header__logos {
  display: flex;
  align-items: center;
  padding: 20px 0px;
  flex-shrink: 0;
}

.header__logos .logo + .logo {
  margin-left: 20px;
}

@media screen and (max-width: 1830px) {
  .header__top, .header__main {
    padding-left: 15px;
    padding-right: 15px;
  }
  .header__recall {
    margin-left: 0px;
  }
}

@media screen and (max-width: 1570px) {
  .header .nav {
    display: none;
  }
  .header .search {
    margin-left: auto;
  }
  .header .burger {
    display: inline-block;
  }
}

@media screen and (max-width: 1410px) {
  .header__top {
    display: none;
  }
  .header__compare {
    padding: 22px 20px;
  }
  .header__logos {
    padding: 10px 0px;
  }
}

@media screen and (max-width: 740px) {
  .header {
    border-bottom: none;
  }
  .header_fixed .header__compare {
    padding: 0;
  }
  .header__main {
    align-items: center;
    background-color: #0357a3;
  }
  .header__compare {
    font-size: 0;
    margin-left: auto;
    border: none;
    padding: 0;
  }
  .header__compare svg {
    fill: #ffffff;
    margin: 0;
  }
  .header .burger {
    background-color: transparent;
    border: none;
  }
  .header .burger span, .header .burger::before, .header .burger::after {
    background-color: #fff;
  }
  .header .search {
    display: none;
  }
  .header.menu-opened .header__logos, .header.menu-opened .header__compare {
    display: none;
  }
  .header.menu-opened .search_mobile {
    display: flex;
  }
}
