@import "./variables";

.nav {
    width: 100%;
    max-width: 1070px;
    margin-left: 50px;
    &__list {
        width: 100%;
        height: 100%;
        list-style-type: none;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        width: 100%;
        &_inner {
            position: absolute !important;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: 2;
            background-color: #f8f9fa;
            align-items: flex-start !important;
            justify-content: flex-start !important;
            transform: translateX(100%);
            @include transition(transform);
            &.active {
                transform: translateX(0%); }
            .nav {
                &__item {
                    height: auto; } } } }
    &__dropdown {
        min-width: 200px;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 2;
        background-color: #fff;
        list-style-type: none;
        opacity: 0;
        visibility: hidden;
        @include transition(all);
        transform: translate(-20px, -30px);
        box-shadow: 0 2px 12px 2px rgba(0, 0, 0, .1);
        &-item {
            display: inline-block;
            width: 100%; } }
    &__dropdown &__link {
        padding: 7px 20px;
        @include transition(all);
        display: inline-block;
        width: 100%;
        &:hover {
            background-color: $border-grey; } }
    &__dropdown-item:first-child &__link {
        padding: 10px 20px 7px; }
    &__dropdown-item:last-child &__link {
        padding: 7px 20px 10px; }
    &__item {
        display: flex;
        align-items: center;
        vertical-align: middle;
        position: relative;
        height: 100%;
        &_contains {
            padding-right: 15px;
            & > .nav__link::after {
                content: '';
                display: inline-block;
                position: absolute;
                top: 5px;
                right: -16px;
                margin-left: 4px;
                transform: translate(-3px, 0);
                @include transition(all);
                border: 5px solid transparent {
                    top: 5px solid $text-black; } }
            &.active {
                & > .nav__link {
                    &::after {
                        border: 5px solid transparent {
                            top: 5px solid $light-blue; } } } }
            &:hover {
                & > .nav__link {
                    color: $light-blue;
                    &::after {
                        transform: translate(-3px, -40%) rotate(180deg);
                        border: 5px solid transparent {
                            top: 5px solid $light-blue; } } }
                .nav__dropdown {
                    opacity: 1;
                    visibility: visible;
                    transform: translate(-20px, 0px); } } }
        &.active {
            &::before {
                content: '';
                display: inline-block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: $light-blue; } } }
    &__divide {
        width: 1px;
        display: inline-block;
        height: 20px;
        background-color: $border-grey; }
    &__link {
        display: inline-block;
        @include transition(color);
        font-size: 16px;
        font-weight: bold;
        text-transform: uppercase;
        position: relative;
        svg {
            @include transition(fill);
            fill: $text-black; }
        &:hover, &.active {
            color: $light-blue;
            svg {
                fill: $light-blue; } } }
    &__item.active > &__link {
        color: $light-blue;
        svg {
            fill: $light-blue; } }
    &_menu {
        margin-left: 0;
        .nav__item + .nav__item {
            border-top: 1px solid $border-grey; }
        .nav {
            &__list {
                flex-direction: column;
                align-items: stretch;
                position: static; }
            &__item {
                width: 100%;
                position: static;
                &:not(.nav__item_return):last-child {
                    border-bottom: 1px solid $border-grey; } }
            &__link {
                width: 100%;
                @include transition(all);
                padding: 12px 15px;
                &_return {
                    color: $light-blue;
                    padding: 22px 15px 22px 30px;
                    &::before {
                        content: '<';
                        color: currentColor;
                        position: absolute;
                        font-weight: 700;
                        font-size: 16px;
                        left: 15px;
                        top: 50%;
                        transform: translate(0, -50%); } }
                &_contains {
                    &::after {
                        content: '';
                        display: inline-block;
                        position: absolute;
                        top: 50%;
                        right: 15px;
                        transform: translate(0, -50%);
                        @include transition(all);
                        border: 5px solid transparent {
                            left: 5px solid $text-black; } } }
                &:hover, &.active {
                    color: $light-blue;
                    background-color: #eeeeee;
                    &::after {
                        border: 5px solid transparent {
                            left: 5px solid $light-blue; } } } } } } }

@media screen and ( max-width: 1830px ) {
    .nav:not(.nav_menu) {
        margin: 0px 15px;
        max-width: 1000px; } }

@media screen and ( max-width: 430px ) {
    .nav {
        &_menu {
            .nav {
                &__link {
                    font-size: 12px; } } } } }
