.article {
  padding-bottom: 80px;
}

.article__body {
  background-color: #fff;
  border: 1px solid #dadbdc;
  padding: 30px;
}

.article__body + .article__body {
  margin-top: 30px;
}

.article__body img {
  max-width: 100%;
}

.article__body > * + * {
  margin-top: 1em;
}

.article__body * {
  line-height: 150%;
}

.article__body h1, .article__body h2, .article__body h3, .article__body h4, .article__body h5 {
  line-height: 120%;
}

.article__body ul {
  list-style-position: inside;
}

@media screen and (max-width: 540px) {
  .article__body {
    padding: 20px;
  }
  .article h1, .article h2, .article h3, .article h4, .article h5 {
    font-size: 18px;
  }
  .article p, .article ul, .article span {
    font-size: 14px;
  }
}
