@import "./variables";

.article {
    padding-bottom: $vertical-padding;
    &__body {
        background-color: #fff;
        border: 1px solid $border-grey;
        padding: 30px;
        & + & {
            margin-top: 30px; }
        img {
            max-width: 100%; }
        & > * + * {
            margin-top: 1em; }
        * {
            line-height: 150%; }
        h1, h2, h3, h4, h5 {
            line-height: 120%; }
        ul {
            list-style-position: inside; } } }

@media screen and ( max-width: 540px ) {
    .article {
        &__body {
            padding: 20px; }
        h1, h2, h3, h4, h5 {
            font-size: 18px; }
        p, ul, span {
            font-size: 14px; } } }
