@import "./variables";

.compare {
    padding-bottom: $vertical-padding;
    &__filter {
        display: flex;
        align-items: center;
        background-color: #fff;
        border: 1px solid $border-grey;
        padding: 15px 30px;
        &-text {
            display: inline-block;
            margin-right: 5px;
            font-size: 16px; } }
    &__filter + &__body {
        margin-top: 10px; }
    &__body {
        overflow-x: auto; } }

@media screen and ( max-width: 960px ) {
    .compare {
        &__filter {
            padding: 15px 20px; } } }

@media screen and ( max-width: 768px ) {
    .compare {
        &__filter {
            padding: 15px 10px; } } }
