@import "./variables";

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
    border-radius: 0px;
    text-decoration: none;
    font-family: 'Proxima Nova', sans-serif;
    color: $text-black; }

body, html {
    overflow-x: hidden; }

.bg {
    &-blue {
        background-color: $main-blue; }
    &-grey {
        background-color: #f8f9fa; } }

body {
    min-height: 100vh;
    display: flex;
    flex-direction: column; }

.header, .footer {
    flex-grow: 0; }

.container {
    width: 100%;
    max-width: 1200px;
    padding: 0px 15px;
    margin: 0 auto;
    position: relative;
    z-index: 2; }

section {
    position: relative; }

.text_white {
    color: #ffffff; }

.align {
    &-center {
        display: block;
        margin: {
            left: auto;
            right: auto; } }
    &-left {
        float: left; }
    &-right {
        float: right; } }
