@import "./variables";

.map {
    position: relative;
    &__data {
        padding: 40px 50px;
        position: relative;
        background-color: #fff;
        border: 1px solid $border-grey;
        dt {
            font-weight: 700;
            font-size: 22px;
            margin-bottom: 10px;
            text-transform: uppercase;
            position: relative;
            &::before {
                content: '';
                display: inline-block;
                height: 0.8em;
                width: 13px;
                position: absolute;
                top: 3px;
                left: -20px;
                background-color: $main-blue; } }
        dd {
            &, & a {
                font-size: 16px;
                line-height: 150%; } }
        dd + dt {
            margin-top: 20px; } }
    &:not(.map_contacts) {
        padding: 30px 0px;
        .map__data {
            padding: 40px 50px;
            z-index: 2;
            background-color: rgba(3, 87, 163, .9);
            border: none;
            max-width: 450px;
            dt, dd {
                &, & a {
                    color: #fff; } }
            dt {
                &::before {
                    background-color: $light-blue; } } }
        iframe {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%; } } }

@media screen and ( max-width: 870px ) {
    .map {
        &:not(.map_contacts) {
            .map {
                &__data {
                    padding: 40px;
                    max-width: none;
                    background-color: transparent;
                    dt, dd {
                        &, & a {
                            color: $text-black; } } } }
            .container {
                padding: 0; }
            iframe {
                min-height: 300px;
                position: static; } } } }

@media screen and ( max-width: 470px ) {
    .map {
        &:not(.map_contacts) {
            iframe {
                min-height: 200px; } } } }
