@import "./variables";

.popup {
    position: fixed;
    z-index: 22;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .4);
    display: flex;
    align-items: center;
    justify-content: center;
    @include transition(all);
    opacity: 0;
    overflow-y: auto;
    visibility: hidden;
    pointer-events: none;
    padding: 10px;
    &__media {
        width: 100%;
        height: 100%;
        object-fit: cover; }
    &__body {
        @include transition(all);
        transform: translateY(-50px);
        opacity: 0;
        width: 100%;
        position: relative;
        height: auto;
        &_media {
            max-width: 90%;
            height: 90%;
            .popup__close {
                background-color: rgba(0, 0, 0, .6); } }
        &_small {
            max-width: 650px; }
        &_big {
            max-width: 1150px; } }
    &__subtext {
        margin-top: 50px;
        font-size: 18px;
        color: #ffffff;
        text-align: center;
        display: inline-block;
        width: 100%; }
    &__close {
        position: absolute;
        top: 0;
        right: 0;
        height: 40px;
        width: 40px;
        z-index: 3;
        &::before, &::after {
            content: '';
            display: inline-block;
            height: 2px;
            width: 22px;
            background-color: #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); }
        &::before {
            transform: translate(-50%, -50%) rotate(-45deg); }
        &::after {
            transform: translate(-50%, -50%) rotate(45deg); } }
    &.active &__body {
        transform: translateY(0px);
        opacity: 1; }
    &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: all; } }

@media screen and ( max-height: 820px ) and (max-width: 1100px) {
    .popup {
        &__body {
            height: 100%; } } }
