.articles {
  padding-bottom: 80px;
}

.articles__filter {
  margin-bottom: 30px;
}

.articles__list {
  margin-bottom: 30px;
}

.article-preview {
  padding: 30px;
  border: 1px solid #dadbdc;
  position: relative;
  display: flex;
  width: 100%;
  background-color: #fff;
  transition: box-shadow 0.25s ease-out 0s;
}

.article-preview__img + .article-preview__content {
  margin-left: 30px;
}

.article-preview__img {
  width: 170px;
  object-fit: cover;
  height: auto;
  max-height: 130px;
}

.article-preview__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.article-preview__date {
  font-size: 12px;
  color: #a8a9aa;
  margin-bottom: 5px;
}

.article-preview__title {
  margin-bottom: 5px;
}

.article-preview__title, .article-preview__title * {
  font-size: 18px;
  line-height: 130%;
}

.article-preview__preview {
  line-height: 150%;
  margin-bottom: 10px;
  display: inline-block;
}

.article-preview__permalink {
  color: #2ca8e1;
  transition: color 0.25s ease-out 0s;
}

.article-preview__permalink::before {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.article-preview__permalink:hover {
  color: #52bbec;
}

.article-preview + .article-preview {
  margin-top: 10px;
}

.article-preview:hover {
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 740px) {
  .article-preview {
    padding: 20px;
    flex-direction: column;
  }
  .article-preview__title, .article-preview__title * {
    font-size: 16px;
  }
  .article-preview__preview {
    font-size: 14px;
  }
  .article-preview__img + .article-preview__content {
    margin-left: 0px;
    margin-top: 20px;
  }
  .articles__filter {
    background-color: #fff;
    border: 1px solid #dadbdc;
    padding: 15px 10px;
  }
}
