@import "./variables";

.menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 15;
    background-color: #f8f9fa;
    transform: translateY(-100%);
    pointer-events: none;
    @include transition(transform);
    overflow-y: auto;
    visibility: hidden;
    &.active {
        visibility: visible;
        transform: translateY(0%);
        pointer-events: all; } }
