@import "./variables";

.hero {
    margin-bottom: 70px;
    &__control {
        position: absolute;
        height: 50px;
        width: 50px;
        background-color: rgba(0, 0, 0, .4);
        top: 50%;
        z-index: 2;
        &_prev {
            left: 60px;
            transform: translate(0, -50%); }
        &_next {
            right: 60px;
            transform: translate(0, -50%) rotate(180deg); }
        svg {
            height: 20px;
            width: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #ffffff; }
        &:hover {
            background-color: rgba(0, 0, 0, .8); } }
    &__slide {
        position: relative;
        padding-bottom: 26.8%;
        display: flex;
        overflow: hidden;
        min-height: 370px;
        .container {
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%); } }
    &__bg {
        height: 100%;
        width: 100%;
        position: absolute;
        img {
            object-fit: cover;
            height: 100%;
            width: 100%; } }
    &__body {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        background-color: rgba(3, 87, 163, .8);
        padding: 25px 30px;
        max-width: 670px;
        position: relative; }
    &__title, &__item {
        color: #ffffff; }
    &__title {
        font-size: 26px;
        font-weight: 700;
        display: inline-block;
        margin-bottom: 20px; }
    &__list {
        list-style-type: none;
        display: grid;
        grid-auto-flow: column;
        grid-template-rows: 1fr 1fr;
        row-gap: 10px;
        column-gap: 15px; }
    &__item {
        display: inline-block; }
    &__link {
        display: inline-block;
        text-transform: uppercase;
        font-weight: bold;
        color: #ffffff;
        background-color: $light-blue;
        padding: 15px 20px;
        transform: translate(50px, 25px);
        @include transition(background-color);
        &:hover {
            background-color: $light-blue-hover; } }
    .swiper-pagination {
        position: static;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 30px;
        &-bullet {
            background-color: $border-grey;
            opacity: 1;
            border-radius: 4px;
            @include transition(width);
            &-active {
                width: 30px;
                background-color: $border-grey; } } } }

@media screen and ( max-width: 1410px ) {
    .hero {
        &__control {
            top: 40%;
            height: 40px;
            width: 40px;
            svg {
                height: 15px; }
            &_prev {
                left: 30px; }
            &_next {
                right: 30px; } } } }

@media screen and ( max-width: 1230px ) {
    .hero {
        &__control {
            &_prev {
                left: 15px; }
            &_next {
                right: 15px; } } } }

@media screen and ( max-width: 740px ) {
    .hero {
        &__slide {
            padding-bottom: 0;
            display: block;
            .container {
                position: relative;
                transform: none;
                left: auto;
                padding: 0; } }
        &__bg {
            position: relative;
            min-height: 380px;
            height: 100%; }
        &__img {
            position: absolute;
            width: 100%;
            height: 100%; }
        &__body {
            background-color: $main-blue;
            padding-left: 20px;
            padding-right: 20px;
            max-width: none;
            flex-direction: column;
            align-items: flex-start; }
        &__link {
            padding: 10px 15px;
            margin-top: 20px;
            transform: none; } } }

@media screen and ( max-width: 540px ) {
    .hero {
        &__title {
            font-size: 20px;
            line-height: 25px; }
        &__list {
            grid-template-rows: repeat(4, 1fr); }
        &__bg {
            min-height: 280px; } } }

@media screen and ( max-width: 430px ) {
    .hero {
        &__control {
            display: none; }
        &__bg {
            min-height: 180px; }
        &__link {
            padding: 5px 15px; } } }
