.nav {
  width: 100%;
  max-width: 1070px;
  margin-left: 50px;
}

.nav__list {
  width: 100%;
  height: 100%;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100%;
}

.nav__list_inner {
  position: absolute !important;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  background-color: #f8f9fa;
  align-items: flex-start !important;
  justify-content: flex-start !important;
  transform: translateX(100%);
  transition: transform 0.25s ease-out 0s;
}

.nav__list_inner.active {
  transform: translateX(0%);
}

.nav__list_inner .nav__item {
  height: auto;
}

.nav__dropdown {
  min-width: 200px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
  list-style-type: none;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease-out 0s;
  transform: translate(-20px, -30px);
  box-shadow: 0 2px 12px 2px rgba(0, 0, 0, 0.1);
}

.nav__dropdown-item {
  display: inline-block;
  width: 100%;
}

.nav__dropdown .nav__link {
  padding: 7px 20px;
  transition: all 0.25s ease-out 0s;
  display: inline-block;
  width: 100%;
}

.nav__dropdown .nav__link:hover {
  background-color: #dadbdc;
}

.nav__dropdown-item:first-child .nav__link {
  padding: 10px 20px 7px;
}

.nav__dropdown-item:last-child .nav__link {
  padding: 7px 20px 10px;
}

.nav__item {
  display: flex;
  align-items: center;
  vertical-align: middle;
  position: relative;
  height: 100%;
}

.nav__item_contains {
  padding-right: 15px;
}

.nav__item_contains > .nav__link::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 5px;
  right: -16px;
  margin-left: 4px;
  transform: translate(-3px, 0);
  transition: all 0.25s ease-out 0s;
  border: 5px solid transparent;
  border-top: 5px solid #1c1d1e;
}

.nav__item_contains.active > .nav__link::after {
  border: 5px solid transparent;
  border-top: 5px solid #2ca8e1;
}

.nav__item_contains:hover > .nav__link {
  color: #2ca8e1;
}

.nav__item_contains:hover > .nav__link::after {
  transform: translate(-3px, -40%) rotate(180deg);
  border: 5px solid transparent;
  border-top: 5px solid #2ca8e1;
}

.nav__item_contains:hover .nav__dropdown {
  opacity: 1;
  visibility: visible;
  transform: translate(-20px, 0px);
}

.nav__item.active::before {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #2ca8e1;
}

.nav__divide {
  width: 1px;
  display: inline-block;
  height: 20px;
  background-color: #dadbdc;
}

.nav__link {
  display: inline-block;
  transition: color 0.25s ease-out 0s;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
}

.nav__link svg {
  transition: fill 0.25s ease-out 0s;
  fill: #1c1d1e;
}

.nav__link:hover, .nav__link.active {
  color: #2ca8e1;
}

.nav__link:hover svg, .nav__link.active svg {
  fill: #2ca8e1;
}

.nav__item.active > .nav__link {
  color: #2ca8e1;
}

.nav__item.active > .nav__link svg {
  fill: #2ca8e1;
}

.nav_menu {
  margin-left: 0;
}

.nav_menu .nav__item + .nav__item {
  border-top: 1px solid #dadbdc;
}

.nav_menu .nav__list {
  flex-direction: column;
  align-items: stretch;
  position: static;
}

.nav_menu .nav__item {
  width: 100%;
  position: static;
}

.nav_menu .nav__item:not(.nav__item_return):last-child {
  border-bottom: 1px solid #dadbdc;
}

.nav_menu .nav__link {
  width: 100%;
  transition: all 0.25s ease-out 0s;
  padding: 12px 15px;
}

.nav_menu .nav__link_return {
  color: #2ca8e1;
  padding: 22px 15px 22px 30px;
}

.nav_menu .nav__link_return::before {
  content: '<';
  color: currentColor;
  position: absolute;
  font-weight: 700;
  font-size: 16px;
  left: 15px;
  top: 50%;
  transform: translate(0, -50%);
}

.nav_menu .nav__link_contains::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translate(0, -50%);
  transition: all 0.25s ease-out 0s;
  border: 5px solid transparent;
  border-left: 5px solid #1c1d1e;
}

.nav_menu .nav__link:hover, .nav_menu .nav__link.active {
  color: #2ca8e1;
  background-color: #eeeeee;
}

.nav_menu .nav__link:hover::after, .nav_menu .nav__link.active::after {
  border: 5px solid transparent;
  border-left: 5px solid #2ca8e1;
}

@media screen and (max-width: 1830px) {
  .nav:not(.nav_menu) {
    margin: 0px 15px;
    max-width: 1000px;
  }
}

@media screen and (max-width: 430px) {
  .nav_menu .nav__link {
    font-size: 12px;
  }
}
