@import "./variables";

.item-card {
    background-color: #fff;
    border: 1px solid $border-grey;
    position: relative;
    &__img {
        width: 100%;
        height: 150px;
        object-fit: cover; }
    &__compare {
        position: absolute;
        z-index: 2;
        top: 10px;
        right: 10px;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        background-color: rgba(255, 255, 255, .9);
        @include transition(background-color);
        svg {
            @include transition(fill);
            position: absolute;
            top: 50%;
            left: 50%;
            height: auto;
            transform: translate(-50%, -50%);
            fill: $grey; }
        &:hover, &.active {
            background-color: rgba(44, 168, 225, .9);
            svg {
                fill: #ffffff; } } }
    &__title {
        font-size: 18px;
        font-weight: 700; }
    &__list {
        list-style-type: none;
        margin-top: 5px; }
    &__item {
        color: $grey;
        font-size: 14px;
        & + & {
            margin-top: 3px; } }
    &__body {
        padding: 20px; }
    &__bottom {
        margin: 0px 10px;
        padding: 20px 10px;
        border-top: 1px solid $border-grey; }
    &__price {
        font-size: 22px;
        font-weight: 700;
        &-old {
            font-size: 16px;
            color: $grey;
            font-weight: 700;
            display: inline-block;
            margin-left: 1em;
            text-decoration: line-through; } }
    &__buttons {
        margin-top: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .button {
            flex-grow: 1;
            &_transparent {
                flex-basis: 185px; } }
        .button + .button {
            margin-left: 20px; } }
    &__cart {
        padding: 6px;
        svg {
            @include transition(fill);
            fill: $grey;
            margin-right: 0; }
        &:hover {
            svg {
                fill: $light-blue-hover; } } } }
