@import "./variables";

.logo {
    flex-shrink: 0;
    width: 112px;
    display: block;
    overflow: hidden;
    &__img, img, svg {
        width: 100%;
        height: 40px;
        max-width: 100%;
        object-fit: cover;
        fill: $main-blue; }
    &_small {
        max-width: 98px; }
    &_abs {
        height: 40px;
        position: relative;
        svg, img, &__img {
            position: absolute;
            width: 200px;
            height: 230px;
            max-width: none;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } } }

@media screen and ( max-width: 740px ) {
    .logo {
        &__img, img, svg {
            fill: #ffffff; } } }

@media screen and ( max-width: 400px ) {
    .logo {
        &, &_small {
            width: 80px; }
        &_abs {
            svg, img, &__img {
                height: 180px; } } } }
