@import "./variables";

.tab {
    width: 100%;
    height: 100%;
    display: none;
    img {
        max-width: 100%; }
    ul {
        list-style-position: inside; }
    strong, p, span {
        display: inline-block; }
    & > * + * {
        line-height: 150%;
        &:not(.no-mt) {
            margin-top: 1.4em; } }
    &.active {
        display: block; }
    &__title {
        padding: 17px;
        width: 100%;
        max-width: 270px;
        border: 1px solid $border-grey;
        color: $grey;
        & + & {
            margin-left: 20px; }
        &.active {
            z-index: 3;
            border-bottom: none;
            background-color: #fff;
            color: $light-blue-hover; } } }

@media screen and ( max-width: 768px ) {
    .tab {
        &__title {
            border-radius: 4px;
            max-width: 350px;
            &.active {
                color: #ffffff;
                background-color: $light-blue; }
            & + & {
                margin-left: 0;
                margin-top: 10px; } } } }
