.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown__trigger {
  position: relative;
  z-index: 3;
}

.dropdown__trigger::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 7px;
  right: -16px;
  margin-left: 4px;
  transform: translate(-3px, 0);
  transition: all 0.25s ease-out 0s;
  border: 4px solid transparent;
  border-top: 4px solid #1c1d1e;
}

.dropdown__trigger.active::after {
  transform: translate(-3px, -3px) rotate(180deg);
}

.dropdown__trigger-text, .dropdown__trigger-text * {
  font-size: 16px;
  font-weight: 400;
  text-transform: none;
}

.dropdown__list {
  overflow: hidden;
  position: absolute;
  min-width: 180px;
  width: calc( 100% + 10px);
  top: 105%;
  left: 0;
  background-color: #fff;
  list-style-type: none;
  transition: all 0.25s ease-out 0s;
  opacity: 0;
  transform: translate(-5px, -20px);
  visibility: hidden;
}

.dropdown__list.active {
  opacity: 1;
  transform: translate(-5px, 0px);
  visibility: visible;
}

.dropdown__item {
  cursor: pointer;
}

.dropdown select {
  visibility: hidden;
  opacity: 0;
  height: 1px;
  width: 1px;
  position: absolute;
}

.dropdown .js-dropdown-select {
  display: none;
}

.filter .dropdown__trigger-text, .filter .dropdown__trigger-text * {
  color: #52bbec;
}

.filter .dropdown__trigger::after {
  border: 4px solid transparent;
  border-top: 4px solid #52bbec;
}

.filter .dropdown__list {
  box-shadow: 0 4px 14px 3px rgba(0, 0, 0, 0.1);
}

.filter .dropdown__item a {
  width: 100%;
  display: inline-block;
  padding: 5px;
  transition: background-color 0.25s ease-out 0s;
}

.filter .dropdown__item:hover a {
  background-color: #dadbdc;
}
