.map {
  position: relative;
}

.map__data {
  padding: 40px 50px;
  position: relative;
  background-color: #fff;
  border: 1px solid #dadbdc;
}

.map__data dt {
  font-weight: 700;
  font-size: 22px;
  margin-bottom: 10px;
  text-transform: uppercase;
  position: relative;
}

.map__data dt::before {
  content: '';
  display: inline-block;
  height: 0.8em;
  width: 13px;
  position: absolute;
  top: 3px;
  left: -20px;
  background-color: #0357a3;
}

.map__data dd, .map__data dd a {
  font-size: 16px;
  line-height: 150%;
}

.map__data dd + dt {
  margin-top: 20px;
}

.map:not(.map_contacts) {
  padding: 30px 0px;
}

.map:not(.map_contacts) .map__data {
  padding: 40px 50px;
  z-index: 2;
  background-color: rgba(3, 87, 163, 0.9);
  border: none;
  max-width: 450px;
}

.map:not(.map_contacts) .map__data dt, .map:not(.map_contacts) .map__data dt a, .map:not(.map_contacts) .map__data dd, .map:not(.map_contacts) .map__data dd a {
  color: #fff;
}

.map:not(.map_contacts) .map__data dt::before {
  background-color: #2ca8e1;
}

.map:not(.map_contacts) iframe {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

@media screen and (max-width: 870px) {
  .map:not(.map_contacts) .map__data {
    padding: 40px;
    max-width: none;
    background-color: transparent;
  }
  .map:not(.map_contacts) .map__data dt, .map:not(.map_contacts) .map__data dt a, .map:not(.map_contacts) .map__data dd, .map:not(.map_contacts) .map__data dd a {
    color: #1c1d1e;
  }
  .map:not(.map_contacts) .container {
    padding: 0;
  }
  .map:not(.map_contacts) iframe {
    min-height: 300px;
    position: static;
  }
}

@media screen and (max-width: 470px) {
  .map:not(.map_contacts) iframe {
    min-height: 200px;
  }
}
