.catalog-item {
  background-color: #0357a3;
  display: flex;
}

.catalog-item__body {
  padding: 30px;
  position: relative;
  flex: 0 0 370px;
}

.catalog-item__img {
  display: block;
  position: relative;
  flex-grow: 1;
  height: 100%;
  overflow: hidden;
}

.catalog-item__img img {
  transition: transform 0.25s ease-out 0s;
  transition-duration: .4s;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.catalog-item__title {
  font-size: 26px;
  line-height: 130%;
  font-weight: 700;
  padding-bottom: 15px;
  color: #ffffff;
  border-bottom: 1px solid #81abd1;
}

.catalog-item__name {
  color: #ffffff;
  display: inline-block;
  margin-top: 20px;
  text-transform: uppercase;
  font-size: 16px;
}

.catalog-item__link {
  position: absolute;
  display: inline-block;
  bottom: 0;
  right: 0;
  font-weight: 700;
  font-size: 12px;
  color: #ffffff;
  padding: 10px 20px;
  text-transform: uppercase;
  background-color: #2ca8e1;
  transition: background-color 0.25s ease-out 0s;
}

.catalog-item__link:hover {
  background-color: #52bbec;
}

.catalog-item:hover .catalog-item__img img {
  transform: scale(1.02);
}

@media screen and (max-width: 960px) {
  .catalog-item {
    height: auto !important;
    flex-direction: column;
  }
  .catalog-item__img {
    min-height: 200px;
  }
  .catalog-item__body {
    flex: auto;
  }
  .catalog-item__name {
    display: block;
  }
  .catalog-item__link {
    margin-top: 20px;
    padding: 5px 20px;
    position: relative;
  }
}

@media screen and (max-width: 560px) {
  .catalog-item__title {
    font-size: 16px;
  }
  .catalog-item__name {
    font-size: 12px;
  }
  .catalog-item__img {
    min-height: 110px;
  }
  .catalog-item__body {
    padding: 15px 30px;
  }
}
