@import "./variables";

.faq {
    padding: $vertical-padding 0px;
    .article + & {
        padding-top: 0; } }

.faq-item {
    background-color: #fff;
    border: 1px solid $border-grey;
    @include transition(padding);
    & + & {
        margin-top: 10px; }
    &__title {
        padding: 30px 30px;
        width: 100%;
        text-align: left;
        text-transform: none;
        font-size: 26px;
        @include transition(color);
        &::before, &::after {
            content: '';
            display: inline-block;
            position: absolute;
            top: 50%;
            right: 30px;
            height: 2px;
            background-color: $light-blue;
            width: 32px;
            @include transition(transform); }
        &::before {
            transform: rotate(0deg); }
        &::after {
            transform: rotate(0deg); }
        &:hover {
            color: $light-blue; } }
    &__content {
        overflow: hidden;
        padding-left: 20px;
        padding-right: 10px;
        margin-left: 30px;
        border-left: 2px solid $border-grey;
        max-height: 0;
        opacity: 0;
        visibility: hidden;
        @include transition(all);
        ul {
            list-style-position: inside; }
        & * {
            line-height: 150%; }
        & > * {
            margin-bottom: 1em;
            &:last-child {
                margin-bottom: 0; } } }
    &.active {
        padding-bottom: 40px; }
    &.active &__content {
        max-height: none;
        opacity: 1;
        visibility: visible; }
    &.active &__title {
        color: $main-blue;
        padding-bottom: 20px;
        &::before {
            transform: rotate(90deg); }
        &::after {
            transform: rotate(180deg); } } }

@media screen and ( max-width: 740px ) {
    .faq-item {
        &__title {
            padding: 20px;
            font-size: 22px;
            &::before, &::after {
                right: 20px;
                width: 22px; } }
        &__content {
            margin-left: 20px;
            padding-left: 10px; } } }

@media screen and ( max-width: 560px ) {
    .faq-item {
        &__title {
            font-size: 16px;
            &::before, &::after {
                width: 16px; } } } }
