@import "./variables";

.about {
    padding: $vertical-padding 0px;
    &__content {
        p {
            line-height: 150%; }
        p + p {
            margin-top: 1em; } }
    &__history {
        display: flex;
        justify-content: space-between;
        width: 100%; }
    &__pictures {
        max-width: 370px;
        margin-left: 20px;
        &-img {
            max-width: 100%;
            width: 100%;
            object-fit: cover;
            & + & {
                margin-top: 25px; } } }
    &__timeline {
        display: flex;
        flex-direction: column;
        list-style-type: none;
        &-year {
            display: inline-block;
            padding: 2px 10px;
            font-weight: 700;
            margin-right: 20px;
            color: #ffffff;
            font-size: 18px;
            background-color: $main-blue;
            text-align: center;
            min-width: 64px; }
        li {
            display: flex;
            align-items: center; }
        li + li {
            margin-top: 20px; } }
    &__leaders {
        display: flex;
        align-items: center; }
    &__leader {
        display: flex;
        align-items: center;
        &-img {
            flex-shrink: 0;
            width: 100px;
            height: 100px;
            object-fit: cover;
            box-shadow: 0 0 0 5px #e5eef6;
            border-radius: 50%; }
        &-content {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-left: 25px; }
        &-name {
            font-size: 18px;
            font-weight: 700; }
        &-descr {
            margin-bottom: 5px; }
        & + & {
            margin-left: 90px; } } }

@media screen and ( max-width: 960px ) {
    .about {
        &__leader {
            & + & {
                margin-left: 50px; } } } }

@media screen and ( max-width: 768px ) {
    .about {
        &__history {
            flex-direction: column; }
        &__pictures {
            margin-left: 0;
            margin: 20px auto 0px; }
        &__leaders {
            flex-direction: column;
            align-items: flex-start; }
        &__leader {
            &-img {
                height: 70px;
                width: 70px; }
            & + & {
                margin-left: 0px;
                margin-top: 50px; } }
        &__timeline {
            li {
                font-size: 12px; }
            &-year {
                font-size: 14px;
                margin-right: 10px; } } } }
