.header .burger {
  display: none;
  height: 40px;
  width: 40px;
  flex-shrink: 0;
  border-radius: 4px;
  background-color: #fff;
  border: 1px solid #dadbdc;
  align-self: center;
  margin-left: 15px;
}

.header .burger span, .header .burger::before, .header .burger::after {
  content: '';
  display: inline-block;
  width: 25px;
  height: 2px;
  background-color: #a8a9aa;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: top 0.25s ease-in-out 0.25s, transform 0.25s ease-in-out 0s, opacity 0.1s ease-in-out 0.25s;
}

.header .burger::before {
  top: calc( 9px + 50%);
}

.header .burger::after {
  top: calc( -9px + 50%);
}

.header .burger span {
  transform: translate(-50%, -50%);
}

.header .burger.active span, .header .burger.active::before, .header .burger.active::after {
  height: 4px;
  transform: translate(-50%, -50%);
  top: 50%;
  transition: top 0.25s ease-in-out 0s, transform 0.25s ease-in-out 0.25s, opacity 0.25s ease-in-out 0.1s;
}

.header .burger.active span {
  opacity: 0;
}

.header .burger.active::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.header .burger.active::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
