@import "./variables";

.catalog-item {
    background-color: $main-blue;
    display: flex;
    &__body {
        padding: 30px;
        position: relative;
        flex: 0 0 370px; }
    &__img {
        display: block;
        position: relative;
        flex-grow: 1;
        height: 100%;
        overflow: hidden;
        img {
            @include transition(transform);
            transition-duration: .4s;
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    &__title {
        font-size: 26px;
        line-height: 130%;
        font-weight: 700;
        padding-bottom: 15px;
        color: #ffffff;
        border-bottom: 1px solid #81abd1; }
    &__name {
        color: #ffffff;
        display: inline-block;
        margin-top: 20px;
        text-transform: uppercase;
        font-size: 16px; }
    &__link {
        position: absolute;
        display: inline-block;
        bottom: 0;
        right: 0;
        font-weight: 700;
        font-size: 12px;
        color: #ffffff;
        padding: 10px 20px;
        text-transform: uppercase;
        background-color: $light-blue;
        @include transition(background-color);
        &:hover {
            background-color: $light-blue-hover; } }
    &:hover &__img img {
        transform: scale(1.02); } }

@media screen and ( max-width: 960px ) {
    .catalog-item {
        height: auto !important;
        flex-direction: column;
        &__img {
            min-height: 200px; }
        &__body {
            flex: auto; }
        &__name {
            display: block; }
        &__link {
            margin-top: 20px;
            padding: 5px 20px;
            position: relative; } } }

@media screen and ( max-width: 560px ) {
    .catalog-item {
        &__title {
            font-size: 16px; }
        &__name {
            font-size: 12px; }
        &__img {
            min-height: 110px; }
        &__body {
            padding: 15px 30px; } } }
