.index-catalog {
  margin-bottom: 80px;
}

.index-catalog__top {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 30px;
}

.index-catalog__top .button + .button {
  margin-left: 60px;
}

.index-catalog__body {
  display: grid;
  grid-template-columns: 1fr;
}

.index-catalog__item {
  height: 200px;
  width: 100%;
  align-items: stretch;
}

.index-catalog__item + .index-catalog__item {
  margin-top: 30px;
}

.index-catalog__item:nth-child(even) {
  flex-direction: row-reverse;
}

@media screen and (max-width: 960px) {
  .index-catalog__item {
    flex-direction: column-reverse;
  }
  .index-catalog__item:nth-child(even) {
    flex-direction: column-reverse;
  }
}

@media screen and (max-width: 740px) {
  .index-catalog__top {
    flex-direction: column;
    align-items: center;
  }
  .index-catalog__top .button + .button {
    margin-left: 0px;
    margin-top: 25px;
  }
}
