@import "./variables";

.gallery {
    padding-bottom: $vertical-padding;
    &__grid {
        display: grid;
        column-gap: 30px;
        row-gap: 30px;
        grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
        margin-bottom: 40px;
        &_video {
            grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
            .gallery-item {
                border: none;
                &__body {
                    padding: 15px 0px 0px;
                    background-color: transparent; }
                &__name {
                    font-size: 16px;
                    font-weight: 400;
                    text-align: left; } } } } }

.gallery-item {
    border: 1px solid $border-grey;
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    &__media {
        display: block;
        position: relative;
        padding-bottom: 75%;
        overflow: hidden;
        &_video {
            &::before {
                background-image: url(../img/play-btn.png);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                content: '';
                display: inline-block;
                height: 60px;
                width: 60px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 2;
                pointer-events: none;
                @include transition(transform); }
            &:hover {
                &::before {
                    transform: translate(-50%, -50%) scale(1.07); } } }
        &:hover {
            img {
                transform: scale(1.01); } }
        img {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            object-fit: cover;
            @include transition(transform);
            transition-duration: .5s; } }
    &__body {
        background-color: #fff;
        padding: 25px 10px;
        height: 100%; }
    &__name {
        font-size: 18px;
        font-weight: 700;
        display: inline-block;
        width: 100%;
        text-align: center;
        line-height: 130%; } }

@media screen and ( max-width: 870px ) {
    .gallery {
        &__grid {
            &_video {
                justify-content: center;
                width: 100%;
                grid-template-columns: repeat(auto-fill, minmax(300px, 480px)); } } } }

@media screen and ( max-width: 470px ) {
    .gallery {
        &__grid {
            &_video {
                grid-template-columns: repeat(auto-fill, minmax(250px, 480px)); } } } }
