@import "./variables";

.articles {
    padding-bottom: $vertical-padding;
    &__filter {
        margin-bottom: 30px; }
    &__list {
        margin-bottom: 30px; } }

.article-preview {
    padding: 30px;
    border: 1px solid $border-grey;
    position: relative;
    display: flex;
    width: 100%;
    background-color: #fff;
    @include transition(box-shadow);
    &__img + &__content {
        margin-left: 30px; }
    &__img {
        width: 170px;
        object-fit: cover;
        height: auto;
        max-height: 130px; }
    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start; }
    &__date {
        font-size: 12px;
        color: $grey;
        margin-bottom: 5px; }
    &__title {
        margin-bottom: 5px;
        &, & * {
            font-size: 18px;
            line-height: 130%; } }
    &__preview {
        line-height: 150%;
        margin-bottom: 10px;
        display: inline-block; }
    &__permalink {
        color: $light-blue;
        @include transition(color);
        &::before {
            position: absolute;
            display: block;
            content: '';
            top: 0;
            left: 0;
            right: 0;
            bottom: 0; }
        &:hover {
            color: $light-blue-hover; } }
    & + & {
        margin-top: 10px; }
    &:hover {
        box-shadow: 0 0 20px 2px rgba(0, 0, 0, .1); } }

@media screen and ( max-width: 740px ) {
    .article-preview {
        padding: 20px;
        flex-direction: column;
        &__title {
            &, & * {
                font-size: 16px; } }
        &__preview {
            font-size: 14px; }
        &__img + &__content {
            margin-left: 0px;
            margin-top: 20px; } }

    .articles {
        &__filter {
            background-color: #fff;
            border: 1px solid $border-grey;
            padding: 15px 10px; } } }
