.logo {
  flex-shrink: 0;
  width: 112px;
  display: block;
  overflow: hidden;
}

.logo__img, .logo img, .logo svg {
  width: 100%;
  height: 40px;
  max-width: 100%;
  object-fit: cover;
  fill: #0357a3;
}

.logo_small {
  max-width: 98px;
}

.logo_abs {
  height: 40px;
  position: relative;
}

.logo_abs svg, .logo_abs img, .logo_abs__img {
  position: absolute;
  width: 200px;
  height: 230px;
  max-width: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 740px) {
  .logo__img, .logo img, .logo svg {
    fill: #ffffff;
  }
}

@media screen and (max-width: 400px) {
  .logo, .logo_small {
    width: 80px;
  }
  .logo_abs svg, .logo_abs img, .logo_abs__img {
    height: 180px;
  }
}
