@import "./variables";

.contacts {
    padding-bottom: $vertical-padding;
    &__body {
        display: flex;
        align-items: stretch;
        & > * {
            width: 100%; }
        & > * + * {
            margin-left: 40px !important; } } }

@media screen and ( max-width: 960px ) {
    .contacts {
        &__body {
            flex-direction: column;
            & > * + * {
                margin-left: 0px !important;
                margin-top: 30px !important; } } } }
