@import "./variables";

.header {
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 20;
    border-bottom: 1px solid $border-grey;
    &_fixed {
        box-shadow: 0 0 14px 3px rgba(0, 0, 0, .1);
        .header {
            &__top {
                padding: 10px 60px; }
            &__logos {
                padding: 10px 0px; }
            &__compare {
                padding: 22px 20px; } } }
    &__top, &__logos, &__compare {
        @include transition(padding); }
    &__top, &__main {
        display: flex;
        align-items: center;
        justify-content: space-between; }
    &__top {
        background-color: $main-blue;
        padding: 20px 60px;
        &-social {
            display: flex;
            align-items: center;
            list-style-type: none; }
        &-content {
            display: flex;
            align-items: center;
            width: 100%;
            justify-content: space-between;
            max-width: 1300px; } }
    &__recall {
        color: $light-blue;
        margin-left: 20px; }
    &__tel {
        display: inline-block;
        vertical-align: middle;
        margin: 0px 10px;
        img, svg {
            margin-right: 10px;
            height: 15px;
            width: auto; } }
    &__contact {
        display: flex;
        align-items: center; }
    &__social {
        position: relative;
        height: 20px;
        width: 20px;
        flex-shrink: 0;
        display: inline-block;
        @include transition(transform);
        &:hover {
            transform: translateY(-3px); }
        &_round {
            border-radius: 50%;
            border: 1px solid #ffffff; }
        &-item {
            display: inline-block;
            & + & {
                margin-left: 10px; } }
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } }
    &__main {
        padding: 0px 60px;
        align-items: stretch;
        background-color: #fff; }
    &__compare {
        padding: 32px 20px;
        height: 100%;
        display: inline-block;
        position: relative;
        @include transition(background-color);
        flex-shrink: 0;
        border: {
            left: 1px solid $border-grey;
            right: 1px solid $border-grey; }
        img, svg {
            @include transition(fill);
            margin-right: 10px;
            vertical-align: baseline;
            fill: #a8a9aa; }
        &:hover {
            background-color: $border-grey;
            svg {
                fill: $light-blue-hover; } } }
    &__logos {
        display: flex;
        align-items: center;
        padding: 20px 0px;
        flex-shrink: 0;
        .logo + .logo {
            margin-left: 20px; } } }

@media screen and ( max-width: 1830px ) {
    .header {
        &__top, &__main {
            $padding: 15px;

            padding-left: $padding;
            padding-right: $padding; }
        &__recall {
            margin-left: 0px; } } }

@media screen and ( max-width: 1570px ) {
    .header {
        .nav {
            display: none; }
        .search {
            margin-left: auto; }

        .burger {
            display: inline-block; } } }

@media screen and ( max-width: 1410px ) {
    .header {
        &__top {
            display: none; }
        &__compare {
            padding: 22px 20px; }
        &__logos {
            padding: 10px 0px; } } }

@media screen and ( max-width: 740px ) {
    .header {
        border-bottom: none;
        &_fixed {
            .header {
                &__compare {
                    padding: 0; } } }
        &__main {
            align-items: center;
            background-color: $main-blue; }
        &__compare {
            font-size: 0;
            margin-left: auto;
            border: none;
            padding: 0;
            svg {
                fill: #ffffff;
                margin: 0; } }
        .burger {
            background-color: transparent;
            border: none;
            span, &::before, &::after {
                background-color: #fff; } }
        .search {
            display: none; }
        &.menu-opened {
            .header {
                &__logos, &__compare {
                    display: none; } }
            .search_mobile {
                display: flex; } } } }
