.button {
  display: inline-block;
  cursor: pointer;
  position: relative;
  user-select: none;
  border: none;
  text-align: center;
  background-color: transparent;
  border-radius: 0px;
  text-transform: uppercase;
  overflow-anchor: none;
  font-weight: 700;
  transition: all 0.25s ease-out 0s;
  font-size: 16px;
}

.button img, .button svg {
  vertical-align: middle;
  margin-right: 10px;
  width: auto;
  height: 100%;
}

.button_transparent {
  padding: 15px;
  border-radius: 4px;
  border: 1px solid;
}

.button_white {
  color: #ffffff;
  border-color: #ffffff;
}

.button_white:hover {
  background-color: #fff;
  color: #0357a3;
}

.button_left {
  text-align: left;
}

.button_grey {
  border-color: #dadbdc;
}

.button_grey:hover {
  background-color: #dadbdc;
}

.button_blue {
  background-color: #2ca8e1;
  color: #ffffff;
}

.button_blue:hover {
  background-color: #52bbec;
}

.button_dashed {
  font-size: 16px;
}

.button_dashed span {
  border-bottom: 2px dashed #a8a9aa;
}

.button_dashed svg {
  fill: #a8a9aa;
}
