@import "./variables";

.footer {
    padding: 55px 0px 0px;
    background-color: $main-blue;
    .header__social {
        margin-top: 30px;
        height: 25px;
        width: 25px; }
    &__wrap {
        display: grid;
        grid-template-columns: 1fr 340px 300px;
        column-gap: 25px;
        margin-bottom: 55px; }
    &__title {
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 600;
        color: #fff;
        padding-bottom: 10px;
        border-bottom: 1px solid #4f8abf;
        margin-bottom: 15px; }
    &__list {
        display: grid;
        grid-auto-flow: column;
        grid-template-rows: repeat(3, 1fr);
        column-gap: 30px;
        &_two-rows {
            grid-template-rows: repeat(2, 1fr); }
        & + & {
            margin-left: 35px; } }
    &__item {
        list-style-type: none;
        display: inline-block; }
    &__link {
        padding: 8px 0px;
        display: inline-block;
        &:hover {
            text-decoration: underline; } }
    &__row {
        display: flex;
        align-items: flex-start; }
    &__text {
        color: #bcd2e6;
        font-size: 14px; }
    &__socials {
        display: flex;
        align-items: center;
        margin-top: 30px; }
    &__social {
        display: inline-block;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: #fff;
        position: relative;
        & + & {
            margin-left: 20px; }
        &:hover {
            transform: translateY(-5px); }
        svg {
            max-width: 25px;
            max-height: 20px;
            width: 100%;
            height: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } }
    &__subscribe {
        position: relative;
        margin-top: 15px;
        display: flex;
        align-items: center;
        &-input {
            background-color: #fff;
            display: block;
            width: 100%;
            padding: 11.5px 20px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            font-size: 14px;
            color: $text-black;
            border: none;
            &:focus {
                outline: none; } } }
    &__enter {
        display: inline-block;
        width: 60px;
        background-color: $light-blue;
        height: 40px;
        cursor: pointer;
        border-radius: 0px;
        border: none;
        flex-shrink: 0;
        position: relative;
        svg {
            width: 25px;
            height: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); }
        &:focus {
            outline: none; } }
    &__bottom {
        background-color: #003d75;
        padding: 20px 0px;
        &-wrap {
            display: flex;
            align-items: center;
            justify-content: space-between; } }
    &__copyright {
        font-size: 14px;
        color: #fff;
        opacity: .5;
        & + & {
            margin-left: 10px; } }
    a.footer__copyright:hover {
        text-decoration: underline; } }

@media screen and ( max-width: 1020px ) {
    .footer {
        &__wrap {
            row-gap: 40px; } } }

@media screen and ( max-width: 740px ) {
    .footer {
        &__wrap {
            grid-template-columns: repeat(1, 1fr); } } }

@media screen and ( max-width: 540px ) {
    .footer {
        &__bottom {
            &-wrap {
                flex-direction: column;
                align-items: flex-start; } }
        &__copyright {
            & + & {
                margin-left: 0px;
                margin-top: 15px; } } } }

@media screen and ( max-width: 380px ) {
    .footer {
        &__list {
            width: 100%;
            column-gap: 15px;
            justify-items: space-between; } } }
