@import "./variables";

.item {
    padding-bottom: $vertical-padding;
    &__body {
        background-color: #fff;
        padding: 30px;
        border: 1px solid $border-grey;
        position: relative;
        z-index: 2;
        &_grid {
            display: grid;
            grid-template-columns: 1fr 330px;
            column-gap: 30px;
            row-gap: 20px; } }
    &__name {
        font-weight: 700;
        font-size: 26px;
        grid-column: 1 / 3; }
    &__top-wrap {
        display: flex;
        align-items: stretch; }
    &__data {
        flex-shrink: 0;
        max-width: 330px; }
    &__slider {
        flex-grow: 1;
        &-wrap {
            width: 100%;
            min-width: 0; } }
    &__tabs &__body {
        margin-top: -1px; }
    &__tabs {
        margin-top: 30px;
        &-top {
            width: 100%;
            display: flex;
            align-items: stretch; } } }

.item-thumbs {
    min-width: 0;
    max-height: 70px;
    height: 100%;
    margin-top: 20px;
    overflow: hidden;
    &__item {
        border: 1px solid $border-grey;
        &.swiper-slide-thumb-active {
            opacity: .7;
            border: 3px solid $light-blue; } }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        cursor: pointer; } }

.item-slider {
    min-width: 0;
    position: relative;
    overflow: hidden;
    width: 100%;
    &__compare {
        position: absolute;
        top: 20px;
        left: 20px;
        height: 40px;
        width: 40px;
        border-radius: 100%;
        background-color: #fff;
        z-index: 3;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, .1);
        @include transition(background-color);
        &:hover {
            background-color: $border-grey;
            svg {
                fill: $light-blue-hover; } }
        svg {
            @include transition(fill);
            position: absolute;
            height: 15px;
            width: 15px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: $grey; } }
    &__control {
        position: absolute;
        height: 40px;
        width: 40px;
        background-color: rgba(0, 0, 0, .4);
        top: 50%;
        transform: translateY(-50%);
        z-index: 3;
        @include transition(background-color);
        &:hover {
            background-color: rgba(0, 0, 0, .8); }
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #ffffff;
            height: 40%; }
        &_prev {
            left: 20px; }
        &_next {
            right: 20px;
            svg {
                transform: translate(-50%, -50%) rotate(180deg); } } }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0; }
    &__slide {
        position: relative;
        padding-bottom: 55%;
        border: 1px solid $border-grey; } }

.data-block {
    &__title {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 15px;
        display: inline-block;
        text-transform: uppercase; }
    &__list {
        list-style-type: none; }
    &__item {
        &_bold {
            font-weight: 700; } }
    &__price {
        display: inline-block;
        font-size: 26px;
        padding: 10px 20px;
        background-color: #fff;
        border-radius: 4px;
        border: 1px solid $border-grey;
        font-weight: 700;
        margin-bottom: 25px; }
    &__small-text {
        font-size: 14px;
        line-height: 150%;
        color: $grey;
        display: inline-block;
        margin-bottom: 20px; }
    &__link {
        color: $light-blue;
        font-size: 16px;
        display: inline-block;
        margin-top: 5px;
        cursor: pointer;
        @include transition(color);
        &:hover {
            color: $light-blue-hover; } }
    &__order {
        padding: 15px;
        margin-bottom: 20px;
        border-radius: 4px; }
    &__order, &__price {
        width: 100%;
        max-width: 270px; }
    &_flex {
        display: flex;
        flex-direction: column;
        align-items: flex-start; }
    & + & {
        margin-top: 35px; } }

@media screen and ( max-width: 1020px ) {
    .item {
        &__body {
            padding: 15px;
            &_grid {
                grid-template-columns: 1fr 280px; } } } }

@media screen and ( max-width: 870px ) {

    .item {
        &__name {
            padding: 0px 15px;
            grid-column: 1 / 2;
            order: 1; }
        &__slider-wrap {
            order: 0;
            width: 100%; }
        &__data {
            padding: 0px 15px;
            order: 2; }
        &__body {
            &_grid {
                padding: 0 0 15px;
                grid-template-columns: 1fr; } } } }

@media screen and ( max-width: 768px ) {
    .item {
        &__tabs &__body {
            margin-top: 10px; }
        &__tabs {
            &-top {
                flex-direction: column;
                align-items: center; } } } }

@media screen and ( max-width: 630px ) {
    .item {
        &__name {
            font-size: 22px; } } }

@media screen and ( max-width: 540px ) {
    .item {
        &__name {
            font-size: 18px; }
        &__thumbs {
            margin: 10px 15px 0px;
            max-height: 50px; } }

    .item-slider {
        &__compare {
            height: 30px;
            width: 30px;
            right: 10px;
            top: 10px;
            left: auto; }
        &__control {
            height: 20px;
            width: 20px;
            &_prev {
                left: 1px; }
            &_next {
                right: 1px; } } }

    .data-block {
        &__title {
            font-size: 16px; }
        &__item {
            font-size: 14px; }
        &__price {
            font-size: 16px; } } }
