.tab {
  width: 100%;
  height: 100%;
  display: none;
}

.tab img {
  max-width: 100%;
}

.tab ul {
  list-style-position: inside;
}

.tab strong, .tab p, .tab span {
  display: inline-block;
}

.tab > * + * {
  line-height: 150%;
}

.tab > * + *:not(.no-mt) {
  margin-top: 1.4em;
}

.tab.active {
  display: block;
}

.tab__title {
  padding: 17px;
  width: 100%;
  max-width: 270px;
  border: 1px solid #dadbdc;
  color: #a8a9aa;
}

.tab__title + .tab__title {
  margin-left: 20px;
}

.tab__title.active {
  z-index: 3;
  border-bottom: none;
  background-color: #fff;
  color: #52bbec;
}

@media screen and (max-width: 768px) {
  .tab__title {
    border-radius: 4px;
    max-width: 350px;
  }
  .tab__title.active {
    color: #ffffff;
    background-color: #2ca8e1;
  }
  .tab__title + .tab__title {
    margin-left: 0;
    margin-top: 10px;
  }
}
