.contacts {
  padding-bottom: 80px;
}

.contacts__body {
  display: flex;
  align-items: stretch;
}

.contacts__body > * {
  width: 100%;
}

.contacts__body > * + * {
  margin-left: 40px !important;
}

@media screen and (max-width: 960px) {
  .contacts__body {
    flex-direction: column;
  }
  .contacts__body > * + * {
    margin-left: 0px !important;
    margin-top: 30px !important;
  }
}
