.faq {
  padding: 80px 0px;
}

.article + .faq {
  padding-top: 0;
}

.faq-item {
  background-color: #fff;
  border: 1px solid #dadbdc;
  transition: padding 0.25s ease-out 0s;
}

.faq-item + .faq-item {
  margin-top: 10px;
}

.faq-item__title {
  padding: 30px 30px;
  width: 100%;
  text-align: left;
  text-transform: none;
  font-size: 26px;
  transition: color 0.25s ease-out 0s;
}

.faq-item__title::before, .faq-item__title::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 30px;
  height: 2px;
  background-color: #2ca8e1;
  width: 32px;
  transition: transform 0.25s ease-out 0s;
}

.faq-item__title::before {
  transform: rotate(0deg);
}

.faq-item__title::after {
  transform: rotate(0deg);
}

.faq-item__title:hover {
  color: #2ca8e1;
}

.faq-item__content {
  overflow: hidden;
  padding-left: 20px;
  padding-right: 10px;
  margin-left: 30px;
  border-left: 2px solid #dadbdc;
  max-height: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease-out 0s;
}

.faq-item__content ul {
  list-style-position: inside;
}

.faq-item__content * {
  line-height: 150%;
}

.faq-item__content > * {
  margin-bottom: 1em;
}

.faq-item__content > *:last-child {
  margin-bottom: 0;
}

.faq-item.active {
  padding-bottom: 40px;
}

.faq-item.active .faq-item__content {
  max-height: none;
  opacity: 1;
  visibility: visible;
}

.faq-item.active .faq-item__title {
  color: #0357a3;
  padding-bottom: 20px;
}

.faq-item.active .faq-item__title::before {
  transform: rotate(90deg);
}

.faq-item.active .faq-item__title::after {
  transform: rotate(180deg);
}

@media screen and (max-width: 740px) {
  .faq-item__title {
    padding: 20px;
    font-size: 22px;
  }
  .faq-item__title::before, .faq-item__title::after {
    right: 20px;
    width: 22px;
  }
  .faq-item__content {
    margin-left: 20px;
    padding-left: 10px;
  }
}

@media screen and (max-width: 560px) {
  .faq-item__title {
    font-size: 16px;
  }
  .faq-item__title::before, .faq-item__title::after {
    width: 16px;
  }
}
