.hero {
  margin-bottom: 70px;
}

.hero__control {
  position: absolute;
  height: 50px;
  width: 50px;
  background-color: rgba(0, 0, 0, 0.4);
  top: 50%;
  z-index: 2;
}

.hero__control_prev {
  left: 60px;
  transform: translate(0, -50%);
}

.hero__control_next {
  right: 60px;
  transform: translate(0, -50%) rotate(180deg);
}

.hero__control svg {
  height: 20px;
  width: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #ffffff;
}

.hero__control:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.hero__slide {
  position: relative;
  padding-bottom: 26.8%;
  display: flex;
  overflow: hidden;
  min-height: 370px;
}

.hero__slide .container {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}

.hero__bg {
  height: 100%;
  width: 100%;
  position: absolute;
}

.hero__bg img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.hero__body {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  background-color: rgba(3, 87, 163, 0.8);
  padding: 25px 30px;
  max-width: 670px;
  position: relative;
}

.hero__title, .hero__item {
  color: #ffffff;
}

.hero__title {
  font-size: 26px;
  font-weight: 700;
  display: inline-block;
  margin-bottom: 20px;
}

.hero__list {
  list-style-type: none;
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: 1fr 1fr;
  row-gap: 10px;
  column-gap: 15px;
}

.hero__item {
  display: inline-block;
}

.hero__link {
  display: inline-block;
  text-transform: uppercase;
  font-weight: bold;
  color: #ffffff;
  background-color: #2ca8e1;
  padding: 15px 20px;
  transform: translate(50px, 25px);
  transition: background-color 0.25s ease-out 0s;
}

.hero__link:hover {
  background-color: #52bbec;
}

.hero .swiper-pagination {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}

.hero .swiper-pagination-bullet {
  background-color: #dadbdc;
  opacity: 1;
  border-radius: 4px;
  transition: width 0.25s ease-out 0s;
}

.hero .swiper-pagination-bullet-active {
  width: 30px;
  background-color: #dadbdc;
}

@media screen and (max-width: 1410px) {
  .hero__control {
    top: 40%;
    height: 40px;
    width: 40px;
  }
  .hero__control svg {
    height: 15px;
  }
  .hero__control_prev {
    left: 30px;
  }
  .hero__control_next {
    right: 30px;
  }
}

@media screen and (max-width: 1230px) {
  .hero__control_prev {
    left: 15px;
  }
  .hero__control_next {
    right: 15px;
  }
}

@media screen and (max-width: 740px) {
  .hero__slide {
    padding-bottom: 0;
    display: block;
  }
  .hero__slide .container {
    position: relative;
    transform: none;
    left: auto;
    padding: 0;
  }
  .hero__bg {
    position: relative;
    min-height: 380px;
    height: 100%;
  }
  .hero__img {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .hero__body {
    background-color: #0357a3;
    padding-left: 20px;
    padding-right: 20px;
    max-width: none;
    flex-direction: column;
    align-items: flex-start;
  }
  .hero__link {
    padding: 10px 15px;
    margin-top: 20px;
    transform: none;
  }
}

@media screen and (max-width: 540px) {
  .hero__title {
    font-size: 20px;
    line-height: 25px;
  }
  .hero__list {
    grid-template-rows: repeat(4, 1fr);
  }
  .hero__bg {
    min-height: 280px;
  }
}

@media screen and (max-width: 430px) {
  .hero__control {
    display: none;
  }
  .hero__bg {
    min-height: 180px;
  }
  .hero__link {
    padding: 5px 15px;
  }
}
