.item-card {
  background-color: #fff;
  border: 1px solid #dadbdc;
  position: relative;
}

.item-card__img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.item-card__compare {
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  background-color: rgba(255, 255, 255, 0.9);
  transition: background-color 0.25s ease-out 0s;
}

.item-card__compare svg {
  transition: fill 0.25s ease-out 0s;
  position: absolute;
  top: 50%;
  left: 50%;
  height: auto;
  transform: translate(-50%, -50%);
  fill: #a8a9aa;
}

.item-card__compare:hover, .item-card__compare.active {
  background-color: rgba(44, 168, 225, 0.9);
}

.item-card__compare:hover svg, .item-card__compare.active svg {
  fill: #ffffff;
}

.item-card__title {
  font-size: 18px;
  font-weight: 700;
}

.item-card__list {
  list-style-type: none;
  margin-top: 5px;
}

.item-card__item {
  color: #a8a9aa;
  font-size: 14px;
}

.item-card__item + .item-card__item {
  margin-top: 3px;
}

.item-card__body {
  padding: 20px;
}

.item-card__bottom {
  margin: 0px 10px;
  padding: 20px 10px;
  border-top: 1px solid #dadbdc;
}

.item-card__price {
  font-size: 22px;
  font-weight: 700;
}

.item-card__price-old {
  font-size: 16px;
  color: #a8a9aa;
  font-weight: 700;
  display: inline-block;
  margin-left: 1em;
  text-decoration: line-through;
}

.item-card__buttons {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.item-card__buttons .button {
  flex-grow: 1;
}

.item-card__buttons .button_transparent {
  flex-basis: 185px;
}

.item-card__buttons .button + .button {
  margin-left: 20px;
}

.item-card__cart {
  padding: 6px;
}

.item-card__cart svg {
  transition: fill 0.25s ease-out 0s;
  fill: #a8a9aa;
  margin-right: 0;
}

.item-card__cart:hover svg {
  fill: #52bbec;
}
