.about {
  padding: 80px 0px;
}

.about__content p {
  line-height: 150%;
}

.about__content p + p {
  margin-top: 1em;
}

.about__history {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.about__pictures {
  max-width: 370px;
  margin-left: 20px;
}

.about__pictures-img {
  max-width: 100%;
  width: 100%;
  object-fit: cover;
}

.about__pictures-img + .about__pictures-img {
  margin-top: 25px;
}

.about__timeline {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}

.about__timeline-year {
  display: inline-block;
  padding: 2px 10px;
  font-weight: 700;
  margin-right: 20px;
  color: #ffffff;
  font-size: 18px;
  background-color: #0357a3;
  text-align: center;
  min-width: 64px;
}

.about__timeline li {
  display: flex;
  align-items: center;
}

.about__timeline li + li {
  margin-top: 20px;
}

.about__leaders {
  display: flex;
  align-items: center;
}

.about__leader {
  display: flex;
  align-items: center;
}

.about__leader-img {
  flex-shrink: 0;
  width: 100px;
  height: 100px;
  object-fit: cover;
  box-shadow: 0 0 0 5px #e5eef6;
  border-radius: 50%;
}

.about__leader-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 25px;
}

.about__leader-name {
  font-size: 18px;
  font-weight: 700;
}

.about__leader-descr {
  margin-bottom: 5px;
}

.about__leader + .about__leader {
  margin-left: 90px;
}

@media screen and (max-width: 960px) {
  .about__leader + .about__leader {
    margin-left: 50px;
  }
}

@media screen and (max-width: 768px) {
  .about__history {
    flex-direction: column;
  }
  .about__pictures {
    margin-left: 0;
    margin: 20px auto 0px;
  }
  .about__leaders {
    flex-direction: column;
    align-items: flex-start;
  }
  .about__leader-img {
    height: 70px;
    width: 70px;
  }
  .about__leader + .about__leader {
    margin-left: 0px;
    margin-top: 50px;
  }
  .about__timeline li {
    font-size: 12px;
  }
  .about__timeline-year {
    font-size: 14px;
    margin-right: 10px;
  }
}
