.compare {
  padding-bottom: 80px;
}

.compare__filter {
  display: flex;
  align-items: center;
  background-color: #fff;
  border: 1px solid #dadbdc;
  padding: 15px 30px;
}

.compare__filter-text {
  display: inline-block;
  margin-right: 5px;
  font-size: 16px;
}

.compare__filter + .compare__body {
  margin-top: 10px;
}

.compare__body {
  overflow-x: auto;
}

@media screen and (max-width: 960px) {
  .compare__filter {
    padding: 15px 20px;
  }
}

@media screen and (max-width: 768px) {
  .compare__filter {
    padding: 15px 10px;
  }
}
