@import "./variables";

.form {
    padding: $vertical-padding 15px;
    &__container {
        width: 100%;
        box-shadow: 0 0 0 10px $main-blue;
        border: 10px solid #3579b5;
        padding: 40px $vertical-padding;
        background-color: $main-blue;
        position: relative;
        &_contacts {
            margin: 10px; } }
    &__title {
        font-size: 26px;
        text-transform: uppercase;
        position: relative;
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            bottom: -22px;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 2px;
            width: 100px;
            background-color: #fff; } }
    &__title, &__subtitle {
        text-align: center; }
    &__subtitle {
        display: block;
        width: 100%;
        margin-top: 20px; }
    &__body {
        margin-top: 70px; }
    &__row {
        margin-bottom: 25px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
        grid-gap: 30px;
        & + & {
            margin-top: 40px; }
        .button {
            box-shadow: none;
            padding: 15px;
            font-size: 16px;
            border-radius: 4px; } }
    &__item {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 4px;
        padding: 10px;
        background-color: #fff;
        &-data {
            display: flex;
            align-items: center; }
        &-name {
            margin-left: 20px;
            font-size: 16px;
            font-weight: 700; }
        &-prices {
            margin-left: 20px;
            display: flex;
            align-items: flex-end;
            flex-shrink: 0; }
        &-price {
            flex-shrink: 0;
            font-size: 18px;
            font-weight: 700;
            display: inline-block;
            &_old {
                margin-left: 10px;
                color: $grey;
                font-size: 14px;
                text-decoration: line-through; } }
        &-img {
            max-height: 60px;
            width: auto;
            max-width: 100%; } }
    &__input {
        display: block;
        width: 100%;
        height: 100%;
        appearance: none;
        border: none;
        padding: 15px 25px 15px 45px;
        font-size: 16px;
        border-radius: 4px;
        &-wrap {
            position: relative;
            &::before {
                content: attr(data-label);
                left: 0;
                top: -5px;
                position: absolute;
                color: #ffffff;
                font-size: 14px;
                transform: translateY(-100%); }
            &::after {
                content: '';
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 20px;
                transform: translate(0, -50%);
                height: 15px;
                width: 15px;
                opacity: .4; }
            &_user::after {
                background-image: url(../img/user.svg); }
            &_phone::after {
                background-image: url(../img/phone-call_2.svg); }
            &_mail::after {
                background-image: url(../img/phone-call_2.svg); }
            &_addr::after {
                background-image: url(../img/phone-call_2.svg); } }
        &:focus {
            outline: none; } }
    &__aic {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        .button {
            box-shadow: none;
            padding: 15px;
            font-size: 16px;
            border-radius: 4px;
            width: 100%;
            max-width: 300px;
            margin-bottom: 20px; } }
    &__agreement {
        user-select: none;
        cursor: pointer;
        &-text {
            color: #fff;
            font-size: 12px; }
        &-checkbox {
            display: inline-block;
            height: 14px;
            width: 14px;
            background-color: #ffffff80;
            transform: translateY(2px);
            margin-right: 5px;
            background-size: 80% 80%;
            background-repeat: no-repeat;
            background-position: center;
            flex-shrink: 0; }
        input:checked + &-checkbox {
            background-image: url(../img/check.svg); }
        input {
            opacity: 0;
            position: absolute;
            height: 1px;
            width: 1px;
            visibility: hidden; } } }

@media screen and ( max-width: 960px ) {
    .form {
        &__row > *:nth-child(3) {
            grid-column: 1 / 3; }
        &__container {
            &_contacts {
                padding: 30px 20px; } } } }


@media screen and ( max-width: 740px ) {
    .form {
        padding: 30px 0px;
        &__container {
            border: none;
            box-shadow: none;
            padding: 30px 20px; }
        &__title {
            font-size: 28px; }
        &__agreement {
            display: flex;
            align-items: baseline;
            &-text {
                text-align: center;
                display: inline-block; } } } }

@media screen and ( max-width: 540px ) {
    .form {
        &__row > *:nth-child(3) {
            grid-column: 1 / 2; }
        &__item {
            flex-direction: column;
            &-data {
                align-items: flex-start; }
            &-prices {
                margin-left: auto;
                margin-right: 10px;
                margin-top: 15px; }
            &-price {
                font-size: 14px;
                &_old {
                    font-size: 12px; } } } } }

@media screen and ( max-width: 430px ) {
    .form {
        &__title {
            font-size: 20px;
            line-height: 24px; } } }
