.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 15;
  background-color: #f8f9fa;
  transform: translateY(-100%);
  pointer-events: none;
  transition: transform 0.25s ease-out 0s;
  overflow-y: auto;
  visibility: hidden;
}

.menu.active {
  visibility: visible;
  transform: translateY(0%);
  pointer-events: all;
}
