.hits {
  padding: 60px 0px 80px;
}

.hits__slider {
  overflow: hidden;
}

.hits__slider-wrap {
  margin-bottom: 40px;
  position: relative;
}

.hits__control {
  position: absolute;
  height: 50px;
  width: 50px;
  background-color: #2ca8e1;
  top: 50%;
  z-index: 3;
}

.hits__control_prev {
  left: -20px;
  transform: translate(-100%, -50%);
}

.hits__control_next {
  right: -20px;
  transform: translate(100%, -50%) rotate(180deg);
}

.hits__control svg {
  height: 20px;
  width: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #ffffff;
}

.hits__control:hover {
  background-color: #52bbec;
}

@media screen and (max-width: 740px) {
  .hits__control {
    top: auto;
    bottom: 0;
    height: 25px;
    width: 25px;
  }
  .hits__control svg {
    height: 12px;
  }
  .hits__control_prev {
    left: 0px;
    transform: translate(0%, 0%);
  }
  .hits__control_next {
    right: 0px;
    transform: translate(0%, 0%) rotate(180deg);
  }
  .hits__slider-wrap {
    padding-bottom: 40px;
  }
}
