$main-blue: #0357a3;
$grey: #a8a9aa;
$light-blue: #2ca8e1;
$light-blue-hover: #52bbec;

$text-black: #1c1d1e;
$text-red: #d44242;

$hover-yellow: #ff0;
$border-grey: #dadbdc;

$vertical-padding: 80px;

$transition-duration: .25s;

@mixin transition($prop) {
    transition: $prop $transition-duration ease-out 0s; }
