@import "./variables";

.button {
    display: inline-block;
    cursor: pointer;
    position: relative;
    user-select: none;
    border: none;
    text-align: center;
    background-color: transparent;
    border-radius: 0px;
    text-transform: uppercase;
    overflow-anchor: none;
    font-weight: 700;
    @include transition(all);
    font-size: 16px;
    img, svg {
        vertical-align: middle;
        margin-right: 10px;
        width: auto;
        height: 100%; }
    &_transparent {
        padding: 15px;
        border-radius: 4px;
        border: 1px solid; }
    &_white {
        color: #ffffff;
        border-color: #ffffff;
        &:hover {
            background-color: #fff;
            color: $main-blue; } }
    &_left {
        text-align: left; }
    &_grey {
        border-color: $border-grey;
        &:hover {
            background-color: $border-grey; } }
    &_blue {
        background-color: $light-blue;
        color: #ffffff;
        &:hover {
            background-color: $light-blue-hover; } }
    &_dashed {
        font-size: 16px;
        span {
            border-bottom: 2px dashed $grey; }
        svg {
            fill: $grey; } } }
