@import "./variables";

.search {
    width: 100%;
    height: 100%;
    align-self: center;
    max-width: 220px;
    border-radius: 4px;
    border: 1px solid $border-grey;
    position: relative;
    display: flex;
    align-items: stretch;
    margin: 0px 15px;
    &__input {
        background-color: #f8f9fa;
        font-size: 16px;
        padding: 12px 0px 10px 20px;
        border: none;
        width: 100%;
        height: 100%;
        display: block; }
    &__button {
        background-color: #f8f9fa;
        flex-shrink: 0;
        padding: 10px 20px 10px 5px;
        img, svg {
            fill: $border-grey;
            margin-right: 0; } }
    &_mobile {
        display: none;
        border-radius: 0px;
        border: none;
        margin: 15px 0px !important;
        .search {
            &__input {
                font-size: 12px;
                padding: 10px; }
            &__enter {
                padding: 0px 5px;
                font-size: 12px; } } } }
