.form {
  padding: 80px 15px;
}

.form__container {
  width: 100%;
  box-shadow: 0 0 0 10px #0357a3;
  border: 10px solid #3579b5;
  padding: 40px 80px;
  background-color: #0357a3;
  position: relative;
}

.form__container_contacts {
  margin: 10px;
}

.form__title {
  font-size: 26px;
  text-transform: uppercase;
  position: relative;
}

.form__title::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: -22px;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 2px;
  width: 100px;
  background-color: #fff;
}

.form__title, .form__subtitle {
  text-align: center;
}

.form__subtitle {
  display: block;
  width: 100%;
  margin-top: 20px;
}

.form__body {
  margin-top: 70px;
}

.form__row {
  margin-bottom: 25px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  grid-gap: 30px;
}

.form__row + .form__row {
  margin-top: 40px;
}

.form__row .button {
  box-shadow: none;
  padding: 15px;
  font-size: 16px;
  border-radius: 4px;
}

.form__item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 4px;
  padding: 10px;
  background-color: #fff;
}

.form__item-data {
  display: flex;
  align-items: center;
}

.form__item-name {
  margin-left: 20px;
  font-size: 16px;
  font-weight: 700;
}

.form__item-prices {
  margin-left: 20px;
  display: flex;
  align-items: flex-end;
  flex-shrink: 0;
}

.form__item-price {
  flex-shrink: 0;
  font-size: 18px;
  font-weight: 700;
  display: inline-block;
}

.form__item-price_old {
  margin-left: 10px;
  color: #a8a9aa;
  font-size: 14px;
  text-decoration: line-through;
}

.form__item-img {
  max-height: 60px;
  width: auto;
  max-width: 100%;
}

.form__input {
  display: block;
  width: 100%;
  height: 100%;
  appearance: none;
  border: none;
  padding: 15px 25px 15px 45px;
  font-size: 16px;
  border-radius: 4px;
}

.form__input-wrap {
  position: relative;
}

.form__input-wrap::before {
  content: attr(data-label);
  left: 0;
  top: -5px;
  position: absolute;
  color: #ffffff;
  font-size: 14px;
  transform: translateY(-100%);
}

.form__input-wrap::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translate(0, -50%);
  height: 15px;
  width: 15px;
  opacity: .4;
}

.form__input-wrap_user::after {
  background-image: url(../img/user.svg);
}

.form__input-wrap_phone::after {
  background-image: url(../img/phone-call_2.svg);
}

.form__input-wrap_mail::after {
  background-image: url(../img/phone-call_2.svg);
}

.form__input-wrap_addr::after {
  background-image: url(../img/phone-call_2.svg);
}

.form__input:focus {
  outline: none;
}

.form__aic {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.form__aic .button {
  box-shadow: none;
  padding: 15px;
  font-size: 16px;
  border-radius: 4px;
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
}

.form__agreement {
  user-select: none;
  cursor: pointer;
}

.form__agreement-text {
  color: #fff;
  font-size: 12px;
}

.form__agreement-checkbox {
  display: inline-block;
  height: 14px;
  width: 14px;
  background-color: #ffffff80;
  transform: translateY(2px);
  margin-right: 5px;
  background-size: 80% 80%;
  background-repeat: no-repeat;
  background-position: center;
  flex-shrink: 0;
}

input:checked + .form__agreement-checkbox {
  background-image: url(../img/check.svg);
}

.form__agreement input {
  opacity: 0;
  position: absolute;
  height: 1px;
  width: 1px;
  visibility: hidden;
}

@media screen and (max-width: 960px) {
  .form__row > *:nth-child(3) {
    grid-column: 1 / 3;
  }
  .form__container_contacts {
    padding: 30px 20px;
  }
}

@media screen and (max-width: 740px) {
  .form {
    padding: 30px 0px;
  }
  .form__container {
    border: none;
    box-shadow: none;
    padding: 30px 20px;
  }
  .form__title {
    font-size: 28px;
  }
  .form__agreement {
    display: flex;
    align-items: baseline;
  }
  .form__agreement-text {
    text-align: center;
    display: inline-block;
  }
}

@media screen and (max-width: 540px) {
  .form__row > *:nth-child(3) {
    grid-column: 1 / 2;
  }
  .form__item {
    flex-direction: column;
  }
  .form__item-data {
    align-items: flex-start;
  }
  .form__item-prices {
    margin-left: auto;
    margin-right: 10px;
    margin-top: 15px;
  }
  .form__item-price {
    font-size: 14px;
  }
  .form__item-price_old {
    font-size: 12px;
  }
}

@media screen and (max-width: 430px) {
  .form__title {
    font-size: 20px;
    line-height: 24px;
  }
}
