@import "./variables";

.header {
    .burger {
        display: none;
        height: 40px;
        width: 40px;
        flex-shrink: 0;
        border-radius: 4px;
        background-color: #fff;
        border: 1px solid $border-grey;
        align-self: center;
        margin-left: 15px;
        span, &::before, &::after {
            content: '';
            display: inline-block;
            width: 25px;
            height: 2px;
            background-color: $grey;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition:  top $transition-duration ease-in-out $transition-duration, transform $transition-duration ease-in-out 0s, opacity .1s ease-in-out $transition-duration; }
        &::before {
            top: calc( 9px + 50% ); }
        &::after {
            top: calc( -9px + 50% ); }
        span {
            transform: translate(-50%, -50%); }
        &.active {
            span, &::before, &::after {
                height: 4px;
                transform: translate(-50%, -50%);
                top: 50%;
                transition:  top $transition-duration ease-in-out 0s, transform $transition-duration ease-in-out $transition-duration, opacity $transition-duration ease-in-out .1s; }
            span {
                opacity: 0; }
            &::before {
                transform: translate(-50%, -50%) rotate(45deg); }
            &::after {
                transform: translate(-50%, -50%) rotate(-45deg); } } } }
